import React, { Component } from 'react'
import { subscribeUser } from '../../components/Notifications/subscription';
import { FormattedMessage} from 'react-intl';

export default class favoritesChecker extends Component {

    //to save the favorites to local storage
    addFavorites() {
        localStorage.setItem(parseInt(this.props.ListaFavoritos), JSON.stringify(parseInt(this.props.ListaFavoritos)))
        subscribeUser(this.props.ListaFavoritos, "Send");
        if(navigator.userAgent.toLowerCase().indexOf('firefox') > -1){
            window.location.reload();
        }
    }

    //to remove the favorites to local storage
    removeFavorites() {

        localStorage.removeItem(this.props.ListaFavoritos)
        subscribeUser(this.props.ListaFavoritos, "Remove");
        if(navigator.userAgent.toLowerCase().indexOf('firefox') > -1){
            window.location.reload();

        }
    }

    //Just for the favorites tables because of reload the window
    removeFavoritesTable() {
        localStorage.removeItem(this.props.ListaFavoritos)
        subscribeUser(this.props.ListaFavoritos, "Remove");

    }

    //to check if is favorite (this is for the tables)
    checkFavorites() {
        if (parseInt(this.props.ListaFavoritos) === parseInt(localStorage.getItem(this.props.ListaFavoritos))) {
            if (this.props.tableFav === true) {

                return (
                    <a href="##" style={{ color: "#3c8dbc" }} onClick={() => this.removeFavoritesTable()}>
                        <i className="fa fa-fw fa-heart"></i>
                    </a>
                )
            }
            else {
                return (
                    <a href="##" style={{ color: "#3c8dbc" }} onClick={() => this.removeFavorites()}>
                        <i className="fa fa-fw fa-heart"></i>
                    </a>
                )
            }

        }
        else {
            return (
                <a href="##" style={{ color: "#3c8dbc" }} onClick={() => this.addFavorites()}>
                    <i className="fa fa-fw fa-heart-o"></i>
                </a>
            )
        }
    }

    //to check if is favorite (this is for the athletedetail)
    checkFavoritesResume() {
        if (parseInt(this.props.ListaFavoritos) === parseInt(localStorage.getItem(this.props.ListaFavoritos))) {
            return (
                <a href="##" className="btn btn-danger btn-block" onClick={() => this.removeFavoritesTable()}><b><FormattedMessage id="REMOVEFAV" /></b></a>
            )
        }
        else {
            return (
                <a href="##" className="btn btn-info btn-block" onClick={() => this.addFavorites()}><b><FormattedMessage id="ADDFAV"/></b></a>
            )
        }
    }
    //check is the call comes from the detail or from a table
    checkIfTableorResume() {
        if (this.props.AthleteDetail === true) {
            return (
                this.checkFavoritesResume()
            )
        }
        else {
            return (
                this.checkFavorites()
            )
        }
    }

    render() {
        return (
            <div>
                {this.checkIfTableorResume()}
            </div>
        )
    }
}
