import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import { FormattedMessage } from 'react-intl';


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;
var size = window.innerWidth;
var menuSizeTop = 16;
var menuSizeItem = 14;

const style = { fontWeight: "bold", color: "#3c8dbc" };
export default class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      CompetitionList: [],
      EventInfo: [],
      loading: false,
    };
    this.OnInit();
    this.CheckSize();


  }

  componentWillMount() {
    if (JSON.parse(localStorage.getItem('EventID')) === this.props.eventID) {
      if (localStorage.getItem('State')) {
        const rehydrate = JSON.parse(localStorage.getItem('State'))
        this.setState(rehydrate)
        const compList=JSON.parse(localStorage.getItem('competitionList'))
        this.setState({CompetitionList : compList})
      }
      else {
        this.getEventInfo();
        this.getCompetitionList();
      }
    }
    else {
      this.getEventInfo();
      this.getCompetitionList();
    }

  }

  //for menu open and close
  OnInit() {
    if (window.localStorage) {
      if (!localStorage.getItem('firstLoad')) {
        localStorage['firstLoad'] = true;
        window.location.reload();
      }
      else
        localStorage.removeItem('firstLoad');
    }
  }

  componentWillUnmount() {
    localStorage.setItem('State', JSON.stringify(this.state))
    localStorage.setItem('competitionList', JSON.stringify(this.state.CompetitionList))
    localStorage.setItem('EventID', JSON.stringify(this.props.eventID))
  }

  CheckSize() {
    if (size < 800) {
      menuSizeItem = 21;
      menuSizeTop = 23;
    }
  }

  getCompetitionList = async () => {

    const API_CALL = await fetch(
      `${API_URL}/trail/Competitions/${this.props.eventID}/`,
      {
        headers: new Headers({
          Authorization: TOKEN
        })
      }
    );
    const data = await API_CALL.json();
    this.setState({ CompetitionList: data });

  };

  getEventInfo = async () => {

    const API_CALL = await fetch(
      `${API_URL}/trail/Event/${this.props.eventID}/`,
      {
        headers: new Headers({
          Authorization: TOKEN
        })
      }
    );
    const data = await API_CALL.json();
    this.setState({ EventInfo: data, loading: true });
  };

  getFeedBackPage() {
    if (this.state.EventInfo.feedback === 1) {
      return (<li>
        <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/feedback/${this.props.eventID}` }}>
          <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="FEEDBACK" />
        </NavLink>

      </li>)
    }
  }

  render() {

    let menuStatic;
    if (this.state.loading === true) {
      if (this.props.competitionID === 0) {
        menuStatic = (
          <li className="treeview active">
            <a href="##">
              <i style={{ fontSize: `${menuSizeTop}px` }} className="fa  fa-road" />
              <span style={{ fontSize: `${menuSizeTop}px` }}>{(this.state.loading === true) ? " " + this.state.EventInfo.short_name : <FormattedMessage id="LOADING" />}</span>
              <span className="pull-right-container">
                <i className="fa fa-angle-left pull-right" />
              </span>
            </a>
            <ul className="treeview-menu active">
              <li>
                <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/EventParticipants/${this.props.eventID}` }}>
                  <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="INSCRIPTIONS" />
                </NavLink>
              </li>
              <li>
                <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Favorites/${this.props.eventID}` }}>
                  <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="FAVORITES" />
                </NavLink>
              </li>
              {this.getFeedBackPage()}
            </ul>
          </li>
        );
      }
      else {
        menuStatic = (
          <li className="treeview">
            <a href="##">
              <i style={{ fontSize: `${menuSizeTop}px` }} className="fa  fa-road" />
              <span style={{ fontSize: `${menuSizeTop}px` }}>{(this.state.loading === true) ? " " + this.state.EventInfo.short_name : <FormattedMessage id="LOADING" />}</span>
              <span className="pull-right-container">
                <i className="fa fa-angle-left pull-right" />
              </span>
            </a>
            <ul className="treeview-menu active">
              <li>
                <NavLink activeStyle={style} activeStyle={{
                  fontWeight: "bold",
                  color: "red"
                }} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/EventParticipants/${this.props.eventID}` }}>
                  <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="INSCRIPTIONS" />
                </NavLink>
              </li>
              <li>
                <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Favorites/${this.props.eventID}` }}>
                  <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="FAVORITES" />
                </NavLink>
              </li>
              {this.getFeedBackPage()}

            </ul>
          </li>
        );
      }



    } else {
      menuStatic = (
        <li className="active treeview menu-open">
          <a href="##">
            <i className="fa fa-dashboard" />
            <span><FormattedMessage id="LOADING" /> <i class="fa fa-refresh fa-spin"></i></span>
            <span className="pull-right-container">
              <i className="fa fa-angle-left pull-right" />
            </span>
          </a>
        </li>
      );
    }

    return (

      <div>
        <aside className="main-sidebar">
          {/* sidebar: style can be found in sidebar.less */}
          <section className="sidebar">
            {/* sidebar menu: : style can be found in sidebar.less */}
            <ul class="sidebar-menu tree" data-widget="tree">
              {/*MENU Event*/}
              {menuStatic}
              {/*MENU Dynamic*/}
              {this.state.CompetitionList.map(Competition => {
                if (parseInt(this.props.competitionID) === parseInt(Competition.competition_id)) {
                  return (
                    <li key={Competition.competition_id} className="treeview active">
                      <a href="##">
                        <i style={{ fontSize: `${menuSizeTop}px` }} className="fa fa-dashboard" />
                        <span style={{ fontSize: `${menuSizeTop}px` }}>{" " + Competition.name}</span>
                        <span style={{ fontSize: `${menuSizeTop}px` }} className="pull-right-container">
                          <i className="fa fa-angle-left pull-right" />
                        </span>
                      </a>
                      <ul className="treeview-menu">
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/ParticipantsCompetiton/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="INSCRIPTIONS" />
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Checkpoints/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="CROSSING" />
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Winners/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="WINNERS" />
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/EventGiveUp/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="DIDNOTFINISH" />
                          </NavLink>

                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Teams/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="TEAMS" />
                          </NavLink>
                        </li>

                      </ul>
                    </li>
                  );
                }
                else {
                  return (
                    <li key={Competition.competition_id} className="treeview">
                      <a href="##">
                        <i style={{ fontSize: `${menuSizeTop}px` }} className="fa fa-dashboard" />
                        <span style={{ fontSize: `${menuSizeTop}px` }}>{"  " + Competition.name}</span>
                        <span className="pull-right-container">
                          <i style={{ fontSize: `${menuSizeTop}px` }} className="fa fa-angle-left pull-right" />
                        </span>
                      </a>
                      <ul className="treeview-menu">
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/ParticipantsCompetiton/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="INSCRIPTIONS" />
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Checkpoints/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="CROSSING" />
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Winners/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="WINNERS" />
                          </NavLink>
                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/EventGiveUp/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i className="fa fa-circle-o" /> <FormattedMessage id="DIDNOTFINISH" />
                          </NavLink>

                        </li>
                        <li>
                          <NavLink activeStyle={style} style={{ fontSize: `${menuSizeItem}px` }} to={{ pathname: `/Teams/${this.props.eventID}/${Competition.competition_id}/${Competition.long_name}` }}>
                            <i style={{ fontSize: `${menuSizeItem}px` }} className="fa fa-circle-o" /> <FormattedMessage id="TEAMS" />
                          </NavLink>
                        </li>
                      </ul>
                    </li>
                  );
                }

              })}
            </ul>
          </section>

          {/* /.sidebar */}
        </aside>
      </div>
    );
  }
}