import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import Loader from 'react-loader-spinner'
import React from "react";

var style = ["BallTriangle", "ThreeDots", "Bars", "Circles", "Grid", "Puff", "TailSpin", "Triangle", "RevolvingDot", "Watch", "MutatingDots"];
var position = Math.floor(Math.random() * style.length)



export default class App extends React.Component {
   
   componentDidUpdate(prevProps) {
      position = Math.floor(Math.random() * style.length)
   }

   render() {
      return (
         <Loader
            type={style[position]}
            color="#3c8dbc"
            height={100}
            width={100}
         />
      );
   }
}