import React, { Component } from 'react'
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import Loader from "../Loader/Loader";
import ChartBar from "../Chart/bar"
import ChartPie from "../Chart/pie"
import { FormattedMessage } from 'react-intl';


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;

export default class StatsGlobal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loadingCategory: false,
            loadingGender: false,
            loadingCompetitions: false,
            loadingNacionality: false,

            categoryList: [],
            genderList: [],
            competitionList: [],
            nacionalityList: [],
        };
        this.GetCategory();
        this.GetGender();
        this.GetCompetitions();
        this.GetNacionality();
    }

    /*for the first time when page is loaded */
    GetCategory = async e => {
        const API_CALL = await fetch(`${API_URL}/trail/CategoryTotal/${this.props.match.params.EventID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ categoryList: data, loadingCategory: true });

    };
    GetGender = async e => {
        const API_CALL = await fetch(`${API_URL}/trail/GenderTotal/${this.props.match.params.EventID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ genderList: data, loadingGender: true });
    };
    GetCompetitions = async e => {
        const API_CALL = await fetch(`${API_URL}/trail/CompetitionTotal/${this.props.match.params.EventID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ competitionList: data, loadingCompetitions: true });
    };
    GetNacionality = async e => {
        const API_CALL = await fetch(`${API_URL}/trail/NacionalityTotal/${this.props.match.params.EventID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ nacionalityList: data, loadingNacionality: true });
    };

    render() {

        return (
            <div>
                <Header eventID={this.props.match.params.EventID} />
                {/*Send the event id to the MENU, to get the competition list*/}
                <Menu eventID={this.props.match.params.EventID} competitionID={0} />
                {/*CONTENT*/}
                <div>
                    {/* Content Wrapper. Contains page content */}
                    <div className="content-wrapper">

                        {/* Content Header (Page header) */}
                        <section className="content-header">

                        </section>
                        {/* Main content */}
                        {/*Check if data is loading*/}
                        <section className="content">
                            <div className="row">
                                <div className="col-md-6">
                                    {/* general form elements */}
                                    <div className="box box-primary">
                                        <div className="box-header with-border">
                                            <div style={{ textAlign: "center" }} >
                                                <h3><FormattedMessage id="CATEGORY" /></h3>
                                            </div>
                                        </div>
                                        {/* /.box-header */}
                                        {/* form start */}
                                        {(() => {
                                            //Category
                                            if (this.state.loadingCategory === false) {
                                                return (
                                                    <div style={{ textAlign: "center" }} className="box-body">
                                                        <Loader />
                                                    </div>
                                                )
                                            }
                                            else {
                                                return (
                                                    <div className="box-body">
                                                        <ChartBar data={this.state.categoryList} />
                                                    </div>
                                                )
                                            }
                                        })()}
                                    </div>
                                    {/* /.box */}

                                </div>

                                <div className="col-md-6">
                                    {/* general form elements */}
                                    <div className="box box-primary">
                                        <div className="box-header with-border">
                                            <div style={{ textAlign: "center" }} >
                                                <h3><FormattedMessage id="GENDER" /></h3>
                                            </div>
                                        </div>
                                        {/* /.box-header */}
                                        {/* form start */}
                                        {(() => {
                                            //Gender

                                            if (this.state.loadingGender === false) {
                                                return (
                                                    <div style={{ textAlign: "center" }} className="box-body">
                                                        <Loader />
                                                    </div>
                                                )
                                            }
                                            else {

                                                return (
                                                    <div className="box-body">
                                                        <ChartPie data={this.state.genderList} />
                                                    </div>
                                                )
                                            }
                                        })()}

                                    </div>
                                    {/* /.box */}

                                </div>
                                <div className="col-md-6">
                                    {/* general form elements */}
                                    <div className="box box-primary">
                                        <div className="box-header with-border">
                                            <div style={{ textAlign: "center" }} >
                                                <h3><FormattedMessage id="COUNTRY" /></h3>
                                            </div>
                                        </div>
                                        {/* /.box-header */}
                                        {/* form start */}
                                        {(() => {
                                            //Gender
                                            if (this.state.loadingNacionality === false) {
                                                return (
                                                    <div style={{ textAlign: "center" }} className="box-body">
                                                        <Loader />
                                                    </div>
                                                )
                                            }
                                            else {
                                                return (
                                                    <div className="box-body">
                                                        <ChartPie data={this.state.nacionalityList} />
                                                    </div>
                                                )
                                            }
                                        })()}

                                    </div>
                                    {/* /.box */}

                                </div>
                                <div className="col-md-6">
                                    {/* general form elements */}
                                    <div className="box box-primary">
                                        <div className="box-header with-border">
                                            <div style={{ textAlign: "center" }} >
                                                <h3><FormattedMessage id="COMPETITION" /></h3>
                                            </div>
                                        </div>
                                        {/* /.box-header */}
                                        {/* form start */}
                                        {(() => {
                                            //Gender
                                            if (this.state.loadingCompetitions === false) {
                                                return (
                                                    <div style={{ textAlign: "center" }} className="box-body">
                                                        <Loader />
                                                    </div>
                                                )
                                            }
                                            else {
                                                return (
                                                    <div className="box-body">
                                                        <ChartBar data={this.state.competitionList} />
                                                    </div>
                                                )
                                            }
                                        })()}

                                    </div>
                                    {/* /.box */}

                                </div>
                            </div>

                        </section>
                    </div>
                    {/* /.content-wrapper */}
                </div>
                <Footer eventID={this.props.match.params.EventID} />
            </div>
        )
    }
}
