import React, { Component } from 'react'
import { Line } from 'react-chartjs-2';

var options;
var data;

export default class Checkpoint extends Component {
    render() {
        data = {
            labels: this.props.data.map(items => { return (items.Label) }),
            datasets: [
                {
                    fill: true,
                    lineTension: 0.1,
                    backgroundColor: '#90CAF9',
                    borderColor: '#42A5F5',
                    borderDash: [],
                    borderDashOffset: 0.0,
                    borderJoinStyle: 'miter',
                    pointBorderColor: '#263238',
                    pointBackgroundColor: '#263238',
                    pointBorderWidth: 1,
                    pointHoverRadius: 5,
                    pointHoverBackgroundColor: '#263238',
                    pointHoverBorderColor: '#263238',
                    pointHoverBorderWidth: 2,
                    pointRadius: 1,
                    pointHitRadius: 10,
                    data: this.props.data.map(items => { return (items.Value) })
                }
            ]
        };

        options = {
            legend: {
                display: false,
            },
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero: true,
                        min: 0,
                    }
                }]
            }
        };
        return (
            <div>
                <Line data={data} options={options} />

            </div>
        )
    }
}
