import React, { Component } from 'react'
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import { FormattedMessage } from 'react-intl';


export default class feedback extends Component {
    constructor(props) {
        super(props);
        this.state = {
          name: '',
          email: '',
          message: '',
          subject: '',
          isClicked: false,

        };

      }
    onNameChange(event) {
        this.setState({name: event.target.value})
      }
    
      onEmailChange(event) {
        this.setState({email: event.target.value})
      }
    
      onMessageChange(event) {
        this.setState({message: event.target.value})
      }
      onSubjecthange(event) {
        this.setState({subject: event.target.value})
      }
      handleSubmit(event) {
        this.setState({isClicked:true})
        event.preventDefault();
        console.log(this.state);
    }
    
    render() {

        return (
            <div>
                <Header eventID={this.props.match.params.EventID} />
                {/*Send the event id to the MENU, to get the competition list*/}
                <Menu eventID={this.props.match.params.EventID} competitionID={0} />
                {/*CONTENT*/}
                <div>
                    {/* Content Wrapper. Contains page content */}
                    <div className="content-wrapper">

                        {/* Content Header (Page header) */}
                        <section className="content-header">
                            <h1>
                                < FormattedMessage id="FEEDBACK" />
                            </h1>
                        </section>
                        {/* Main content */}
                        <section class="content">
                            <div class="box box-primary">
                                <div class="box-header with-border">
                                    <h3 class="box-title"></h3>
                                </div>
                                {/* /.box-header */}
                                {/* form start */}
                                <form role="form" onSubmit={this.handleSubmit.bind(this)} method="POST" >
                                    <div className="box-body">
                                        <div className="form-group">
                                            <label>< FormattedMessage id="NAME" />:</label>
                                            <input id="name"  value={this.state.name} onChange={this.onNameChange.bind(this)} type="text" required className="form-control" placeholder="O seu nome" autoComplete="off" style={{ backgroundImage: 'url("data:image/png', backgroundRepeat: 'no-repeat', backgroundAttachment: 'scroll', backgroundSize: '16px 18px', backgroundPosition: '98% 50%', cursor: 'auto' }} />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="exampleInputEmail1">< FormattedMessage id="EMAIL" />:</label>
                                            <input type="email"  value={this.state.email} onChange={this.onEmailChange.bind(this)} required  value={this.state.email} className="form-control" placeholder="O seu email" autoComplete="off" style={{ backgroundImage: 'url("data:image/png', backgroundRepeat: 'no-repeat', backgroundAttachment: 'scroll', backgroundSize: '16px 18px', backgroundPosition: '98% 50%', cursor: 'auto' }} />
                                        </div>
                                        <div className="form-group">
                                            <label>< FormattedMessage id="SUBJECT" />:</label>
                                            <input type="text" required value={this.state.subject} onChange={this.onSubjecthange.bind(this)}   className="form-control" placeholder="Assunto desejado" autoComplete="off" style={{ backgroundImage: 'url("data:image/png', backgroundRepeat: 'no-repeat', backgroundAttachment: 'scroll', backgroundSize: '16px 18px', backgroundPosition: '98% 50%', cursor: 'auto' }} />
                                        </div>
                                         <div class="form-group">
                                            <label>< FormattedMessage id="MENSAGE" />:</label>
                                            <textarea class="form-control" value={this.state.message} onChange={this.onMessageChange.bind(this)} required rows="5" placeholder="Insira a sua mensagem"></textarea>
                                         </div>
                                    </div>
                                    {/* /.box-body */}
                                    <div className="box-footer">
                                        <button type="submit" disabled={this.state.isClicked===false?"":"disabled"} className="btn btn-primary">{this.state.isClicked===false? < FormattedMessage id="SEND" /> : <div> < FormattedMessage id="SEND" /> <i class="fa fa-refresh fa-spin"></i></div>}</button>
                                    </div>
                                </form>
                            </div>
                        </section>
                    </div>
                    {/* /.content-wrapper */}
                </div>
                <Footer eventID={this.props.match.params.EventID} />
            </div>
        )
    }
}
