export function FormatTime(time) {
    var TimeSplit = time.split(":")
    var H = TimeSplit[0]
    var M = TimeSplit[1]
    var S = TimeSplit[2]
    if (H.length === 1) {
        H = "0" + H
    }
    if (M.length === 1) {
        M = "0" + M
    }
    if (S.length === 1) {
        S = "0" + S
    }
    return (H + ":" + M + ":" + S)
}
