import React, { Component } from 'react'
import { FormattedMessage } from 'react-intl';

export default class NumberCategories extends Component {
    render() {
        //get the category
        let category = this.props.athleteList.map(athelete => {
            return athelete.category;
        });
        //remove duplicates
        const Tableuniquecategory = [...new Set(category)];
        return (
            <div>
                {/* Info boxes */}
                <div className="col-md-3 col-sm-6 col-xs-12">
                    <div className="info-box">
                        <span className="info-box-icon bg-blue"><i className="ion ion-ios-analytics-outline" /></span>
                        <div className="info-box-content">
                            <span className="info-box-text"><FormattedMessage id="CATEGORY" /></span>
                            <span className="info-box-number">{Tableuniquecategory.length}</span>
                        </div>
                        {/* /.info-box-content */}
                    </div>
                    {/* /.info-box */}
                </div>

            </div>
        )
    }
}
