import React, { Component } from 'react'
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import Loader from "../Loader/Loader";
import Chartbar from "../Chart/bar"
import ChartGender from "../Chart/GenderComp"
import ChartGround from "../Chart/Ground"
import ChartCheckpoint from "../Chart/Checkpoint"
import { FormattedMessage } from 'react-intl';
import Map from "../map/map"
const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;

export default class StatsCompetition extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loadingCategory: false,
            loadingCategoryStart: false,
            loadingGender: false,
            loadingGenderStart: false,
            loadingGenderLast: false,
            loadingCategoryLast: false,
            loadingGround: false,
            loadingCheckpoint: false,
            loadingNacionality: false,
            loadingNacionalityLast: false,
            loadingNacionalityStart: false,
            categoryList: [],
            categoryListStart: [],
            categoryListLast: [],
            genderList: [],
            genderListLast: [],
            GroundList: [],
            CheckpointTotalList: [],
            NacionalityListLast: [],
            NacionalityList: [],
            genderListStart: [],
            NacionalityListStart: [],

        };
        this.GetData();
    }
    GetData() {
        this.GetCategory();
        this.GetGender();
        this.GetCategoryLast();
        this.GetGenderLast();
        this.GetGround();
        this.GetCheckpointTotal();
        this.GetNacionality();
        this.GetNacionalityLast();
        this.GetCategoryStart();
        this.GetGenderStart();
        this.GetNacionalityStart();

    }
    /*for the first time when page is loaded */
    GetCategory = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/CategoryTotal/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ categoryList: data, loadingCategory: true });

    };
    GetCategoryStart = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/CategoryTotalStart/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ categoryListStart: data, loadingCategoryStart: true });

    };
    GetCategoryLast = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/CategoryTotalLast/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ categoryListLast: data, loadingCategoryLast: true });

    };
    GetGender = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/GenderTotal/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ genderList: data, loadingGender: true });

    };
    GetGenderStart = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/GenderTotalStart/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ genderListStart: data, loadingGenderStart: true });

    };
    GetGenderLast = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/GenderTotalLast/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ genderListLast: data, loadingGenderLast: true });

    };
    GetGround = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/Checkpoints/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });

        const data = await API_CALL.json();
        this.setState({ GroundList: data, loadingGround: true });
    };
    GetCheckpointTotal = async e => {
        const API_CALL = await fetch(`${API_URL}/results/AthTotalByCheckpoint/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });

        const data = await API_CALL.json();
        this.setState({ CheckpointTotalList: data, loadingCheckpoint: true });
    };
    GetNacionality = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/NacionalityTotal/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ NacionalityList: data, loadingNacionality: true });

    };
    GetNacionalityStart = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/NacionalityTotalStart/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ NacionalityListStart: data, loadingNacionalityStart: true });

    };
    GetNacionalityLast = async e => {
        const API_CALL = await fetch(`${API_URL}/competition/NacionalityTotalLast/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ NacionalityListLast: data, loadingNacionalityLast: true });

    };

    /*when props change*/
    componentDidUpdate(prevProps) {
        if (prevProps.match.params.CompID !== this.props.match.params.CompID) {
            this.setState({
                loadingCategory: false,
                loadingCategoryStart: false,
                loadingGender: false,
                loadingGenderStart: false,
                loadingGenderLast: false,
                loadingCategoryLast: false,
                loadingGround: false,
                loadingGroundStart: false,

            });
            this.GetData();

        }
    }
    render() {
        return (
            <div>
                <div>
                    <Header eventID={this.props.match.params.EventID} />
                    {/*Send the event id to the MENU, to get the competition list*/}
                    <Menu eventID={this.props.match.params.EventID} competitionID={this.props.match.params.CompID} />
                    {/*CONTENT*/}
                    <div>
                        {/* Content Wrapper. Contains page content */}
                        <div className="content-wrapper">

                            {/* Content Header (Page header) */}
                            <section className="content-header">
                                <h1>
                                    <span >{this.props.match.params.CompName}</span>
                                </h1>
                            </section>
                            {/* Main content */}
                            {/*Check if data is loading*/}
                            <section className="content">
                                <div className="row">
                                    <div className="col-md-6">
                                        {/* general form elements */}
                                        <div className="box box-primary">
                                            <div className="box-header with-border">
                                                <div style={{ textAlign: "center" }} >
                                                    <h3><FormattedMessage id="CATEGORY" /> </h3>
                                                </div>
                                            </div>
                                            {/* /.box-header */}
                                            {/* form start */}
                                            {(() => {
                                                //Category
                                                if (this.state.loadingCategory === false && this.state.loadingCategoryLast === false && this.state.loadingCategoryStart === false) {
                                                    return (
                                                        <div style={{ textAlign: "center" }} className="box-body">
                                                            <Loader />
                                                        </div>
                                                    )
                                                }
                                                else {
                                                    return (

                                                        <div className="box-body">
                                                            <FormattedMessage id="STARTS" >
                                                                {Starts =>
                                                                    <FormattedMessage id="INSCRIPTIONS" >
                                                                        {Inscriptions =>
                                                                            <FormattedMessage id="ARRIVALS" >
                                                                                {Arrivals =>
                                                                                    <Chartbar ISThree={true} Arrivals={Arrivals} Start={Starts} Inscription={Inscriptions} data={this.state.categoryList} dataStart={this.state.categoryListStart} dataLast={this.state.categoryListLast} />
                                                                                }
                                                                            </FormattedMessage>
                                                                        }
                                                                    </FormattedMessage>
                                                                }
                                                            </FormattedMessage>

                                                        </div>
                                                    )
                                                }
                                            })()}
                                        </div>
                                        {/* /.box */}

                                    </div>

                                    <div className="col-md-6">
                                        {/* general form elements */}
                                        <div className="box box-primary">
                                            <div className="box-header with-border">
                                                <div style={{ textAlign: "center" }} >
                                                    <h3><FormattedMessage id="GENDER" /></h3>
                                                </div>
                                            </div>
                                            {/* /.box-header */}
                                            {/* form start */}
                                            {(() => {
                                                //Gender
                                                if (this.state.loadingGenderStart ===false && this.state.loadingGender === false && this.state.loadingGenderLast === false) {
                                                    return (
                                                        <div style={{ textAlign: "center" }} className="box-body">
                                                            <Loader />
                                                        </div>
                                                    )
                                                }
                                                else {
                                                    return (
                                                        <div className="box-body">
                                                            <FormattedMessage id="STARTS" >
                                                                {Starts =>
                                                                    <FormattedMessage id="INSCRIPTIONS" >
                                                                        {Inscriptions =>
                                                                            <FormattedMessage id="ARRIVALS" >
                                                                                {Arrivals =>
                                                                                    <ChartGender Arrivals={Arrivals} Start={Starts} Inscription={Inscriptions} data={this.state.genderList} dataStart={this.state.genderListStart} dataLast={this.state.genderListLast} />
                                                                                }
                                                                            </FormattedMessage>
                                                                        }
                                                                    </FormattedMessage>
                                                                }
                                                            </FormattedMessage>

                                                        </div>
                                                    )

                                                }
                                            })()}

                                        </div>
                                        {/* /.box */}

                                    </div>
                                    <div className="col-md-6">
                                        {/* general form elements */}
                                        <div className="box box-primary">
                                            <div className="box-header with-border">
                                                <div style={{ textAlign: "center" }} >
                                                    <h3><FormattedMessage id="HEIGHT" /></h3>
                                                </div>
                                            </div>
                                            {/* /.box-header */}
                                            {/* form start */}
                                            {(() => {
                                                if (this.state.loadingGround === false) {
                                                    return (
                                                        <div style={{ textAlign: "center" }} className="box-body">
                                                            <Loader />
                                                        </div>
                                                    )
                                                }
                                                else {

                                                    return (
                                                        <div className="box-body">
                                                            <ChartGround data={this.state.GroundList} />
                                                        </div>
                                                    )

                                                }
                                            })()}

                                        </div>
                                        {/* /.box */}

                                    </div>
                                    <div className="col-md-6">
                                        {/* general form elements */}
                                        <div className="box box-primary">
                                            <div className="box-header with-border">
                                                <div style={{ textAlign: "center" }} >
                                                    <h3><FormattedMessage id="CHECKPOINT" /></h3>
                                                </div>
                                            </div>
                                            {/* /.box-header */}
                                            {/* form start */}
                                            {(() => {
                                                //Gender

                                                if (this.state.loadingCheckpoint === false) {
                                                    return (
                                                        <div style={{ textAlign: "center" }} className="box-body">
                                                            <Loader />
                                                        </div>
                                                    )
                                                }
                                                else {
                                                    return (
                                                        <div className="box-body">
                                                            <ChartCheckpoint data={this.state.CheckpointTotalList} />
                                                        </div>
                                                    )

                                                }
                                            })()}

                                        </div>
                                        {/* /.box */}

                                    </div>
                                    <div className="col-md-12">
                                        {/* general form elements */}
                                        <div className="box box-primary">
                                            <div className="box-header with-border">
                                                <div style={{ textAlign: "center" }} >
                                                    <h3><FormattedMessage id="COUNTRY" /></h3>
                                                </div>
                                            </div>
                                            {/* /.box-header */}
                                            {/* form start */}
                                            {(() => {
                                                //Gender

                                                if (this.state.loadingNacionality === false && this.state.loadingNacionalityLast === false && this.state.loadingNacionalityStart === false) {
                                                    return (
                                                        <div style={{ textAlign: "center" }} className="box-body">
                                                            <Loader />
                                                        </div>
                                                    )
                                                }
                                                else {
                                                    return (
                                                        <div className="box-body">
                                                            <FormattedMessage id="STARTS" >
                                                                {Starts =>
                                                                    <FormattedMessage id="INSCRIPTIONS" >
                                                                        {Inscriptions =>
                                                                            <FormattedMessage id="ARRIVALS" >
                                                                                {Arrivals =>
                                                                                    <Chartbar ISThree={true} Arrivals={Arrivals} Start={Starts} Inscription={Inscriptions} data={this.state.NacionalityList} dataStart={this.state.NacionalityListStart} dataLast={this.state.NacionalityListLast} />
                                                                                }
                                                                            </FormattedMessage>
                                                                        }
                                                                    </FormattedMessage>
                                                                }
                                                            </FormattedMessage>
                                                        </div>
                                                    )

                                                }
                                            })()}

                                        </div>
                                        {/* /.box */}

                                    </div>
                                </div>
                            </section>
                        </div>
                        {/* /.content-wrapper */}
                    </div>
                    <Footer eventID={this.props.match.params.EventID} />
                </div >

            </div >
        )
    }
}
