import React, { Component } from "react";
import ReactTable from "react-table";
import "react-table/react-table.css";
import { Link } from "react-router-dom";
import Favorites from "../favorites/favoritesChecker"
import {FormatTime} from "../Scripts/time"
import { FormattedMessage } from 'react-intl';

var size = window.innerWidth;


export default class TableSubscribers extends Component {
  
  CheckSize() {
    if(size > 800){
      return true;
    }
    else{
      return false;
    }
  }

  render() {
    
    //Info for the filters
    //get the category
    let category = this.props.athleteList.map(athelete => {
      return athelete.escalao;
    });
    //remove duplicates
    const Tableuniquecategory = [...new Set(category.sort())];
    //get the nacionality
    let nacionality = this.props.athleteList.map(athelete => {
      return athelete.country;
    });
    //remove duplicates
    const Tableuniquenacionality = [...new Set(nacionality.sort())];
  
    //get the Club
    let club = this.props.athleteList.map(athelete => {
      return athelete.club;
    });
    //remove duplicates
    const Tableuniqueclub = [...new Set(club.sort())];


    //Table
    const columns = [
      {
        Header: "Id",
        accessor: "athlete_id",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        width: 100,
        maxWidth: 100,
        mimWidth: 100,
        show: false
      },
      {
        Header: (size>800)?<FormattedMessage id="POSITION" />:"POS",
        accessor: this.props.position,
        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        width:(size>800)?100:55,
        Filter: ({ filter, onChange }) => (
          <FormattedMessage id="SEARCH" >
          {txt => <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder={txt}
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />}
        </FormattedMessage>
        )
      },
      {
        Header: (size>800)?<FormattedMessage id="PHOTO" /> :"PTL",
        id: "photo",
        show: (size>800)?true:false,
        accessor: "photo",
        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        width:(size>800)?100:60, 
        Cell: row => (
          <img className="profile-user-img img-responsive img-circle" src={row.value}></img>
        )
      },
      {
        Header: (size>800)?<FormattedMessage id="DORSAL" /> :"PTL",
        id: "peitoral",
        accessor: d => Number(d.peitoral),
        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        width:(size>800)?100:60,
        Filter: ({ filter, onChange }) => (
          <FormattedMessage id="SEARCH" >
          {txt => <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder={txt}
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />}
        </FormattedMessage>
        ),
        
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_id}` }}
          >
            <span style={{ color: "#3c8dbc" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: <FormattedMessage id="NAME" />,
        accessor: "name",
        width:(size>800)?300:130,
        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        Filter: ({ filter, onChange }) => (
          <FormattedMessage id="SEARCH" >
          {txt => <input
            onChange={event => onChange(event.target.value)}
            value={filter ? filter.value : ""}
            placeholder={txt}
            style={{
              width: "100%",
              textAlign: "Center"
            }}
          />}
        </FormattedMessage>
        ),
        
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_id}` }}
          >
            <span style={{ color: "#3c8dbc" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: <FormattedMessage id="TEAM" />,
        accessor: "club",
        show : this.CheckSize(),

        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniqueclub.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniqueclub.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: <FormattedMessage id="CATEGORY" />,
        accessor: "escalao",
        width:(size>800)?150:85,
        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquecategory.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
             <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniquecategory.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header:  <FormattedMessage id="COMPETITION" />,
        accessor: "country",
        width:150,
        show : this.CheckSize(),
        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquenacionality.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
           <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniquenacionality.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: <FormattedMessage id="TIME" />,
        accessor: "acumulated_time",
        width:(size>800)?100:70,
        filterable:false,
        sortable:false,
        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        Cell: row => FormatTime(row.value)
        
      },
      {
        Header: (size>800)?"Dif 1" : "Dif 1",
        width:(size>800)?100:70,
        accessor: "dif",
        filterable:false,
        sortable:false,
        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        Cell: row => FormatTime(row.value)
        },
      {
        Header: (size>800)?<FormattedMessage id="FAVORITE" />:"FAV",
        filterable: false,
        sortable:false,
        width:(size>800)?100:50,
        style: {
          display: "flex",
          textAlign: "Center",
          alignItems: "center",
          justifyContent: "center",
        },
        Cell: row => (
          <Favorites ListAthletes={this.props.athleteList} ListaFavoritos={row.original.athlete_id} />
        )
      }
    ];
    return (
      <ReactTable
      defaultSorted={[
        {
          id: "PositionGlobal",
          desc: false
        }
      ]}
      previousText={<FormattedMessage id="PREVIOUS" />}
      nextText={<FormattedMessage id="NEXT" />}
        columns={columns}
        data={this.props.athleteList}
        filterable={false}
        noDataText={<FormattedMessage id="NODATA" />}
        defaultPageSize={3}
        showPagination={false}
        showPaginationTop={false}
        showPaginationBottom={false}
        showPageSizeOptions={false}
        showPageJump={true}
        collapseOnSortingChange={true}
        collapseOnPageChange={true}
        collapseOnDataChange={true}
        freezeWhenExpanded={false}
        sortable={false}
        multiSort={true}
        resizable={true}
        defaultSortDesc={false}
        pageText={<FormattedMessage id="PAGE" />}
        rowsText={""}
        className="-striped -highlight"

      />
    );
  }
}
