import React, { Component } from "react";
import ReactTable from "react-table";
import { Link } from "react-router-dom";
import {FormatTime} from "../Scripts/time"
import { FormattedMessage } from 'react-intl';

export default class TableGlobal extends Component {

  
  render() {
    //Info for the filters
    //Table
    const columns = [
      {
        Header: "Id",
        accessor: "athleteid",
        sortable: false,
        style: {
          textAlign: "Center"
        },

        show: false
      },

      {
        Header: <FormattedMessage id="NAME" />,
        accessor: "athleteName",
        filterable: false,
        sortable: false,

        style: {
          textAlign: "Center"
        },
        Cell: row => (

            <Link
              to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.compID}/${row.original.athleteid}` }}
            >
              <span style={{ color: "#3c8dbc" }}>{row.value} </span>
            </Link>
          )

      },
      {
        Header: <FormattedMessage id="TIME" />,
        accessor: "time",
        filterable: false,
        sortable: false,
        style: {
          textAlign: "Center"
        },
        Cell: row => FormatTime(row.value)

      }
    ];
    return (
      <ReactTable
        columns={columns}
        data={this.props.athleteList}
        filterable
        noDataText={<FormattedMessage id="NODATA" />}
        defaultPageSize={parseInt(this.props.athleteList.length)}
        showPagination={false}
        showPaginationTop={false}
        showPaginationBottom={false}
        showPageSizeOptions={false}
        showPageJump={false}
        collapseOnSortingChange={true}
        collapseOnPageChange={true}
        collapseOnDataChange={true}
        freezeWhenExpanded={false}
        sortable={false}
        multiSort={false}
        resizable={false}
        defaultSortDesc={false}
        pageText={<FormattedMessage id="PAGE" />}
        rowsText={""}
        className="-striped -highlight"
      />
    );
  }
}
