import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import App from "../App";
import PageSubscribers from "./Pages/Participants"
import PageGiveUp from "./Pages/DontFinished"
import PageSubscriversCompetiton from "./Pages/ParicipantsCompetiton"
import PageCheckpoints from "./Pages/Checkpoints"
import PageWinners from "./Pages/Winners"
import PageAthleteDetail from "./Pages/AthleteDetail"
import PageTeams from "./Pages/Teams"
import PageFavorites from "./Pages/Favorites"
import PageStatsGlobal from "./Pages/StatsGlobal"
import PageStatsComp from "./Pages/StatsCompetition"
import PageFeedback from "./Pages/feedback"


const Router = () => (
  <BrowserRouter >
    <Switch>
      <Route path="/" component={App} exact />
      <Route path="/EventParticipants/:EventID"   component={PageSubscribers} />   
      <Route path="/AthleteDetail/:EventID/:CompID/:AthleteID"  component={PageAthleteDetail} /> 
      <Route path="/EventGiveUp/:EventID/:CompID/:CompName"  component={PageGiveUp} /> 
      <Route path="/ParticipantsCompetiton/:EventID/:CompID/:CompName"  component={PageSubscriversCompetiton} /> 
      <Route path="/Checkpoints/:EventID/:CompID/:CompName"  component={PageCheckpoints} /> 
      <Route path="/Teams/:EventID/:CompID/:CompName"  component={PageTeams} /> 
      <Route path="/Winners/:EventID/:CompID/:CompName"  component={PageWinners} /> 
      <Route path="/Favorites/:EventID"  component={PageFavorites} /> 
      <Route path="/Stats/:EventID"  component={PageStatsGlobal} /> 
      <Route path="/Feedback/:EventID"  component={PageFeedback} /> 
      <Route path="/StatsComp/:EventID/:CompID/:CompName"  component={PageStatsComp} /> 
    </Switch>
  </BrowserRouter>
);

export default Router