import React, { Component } from "react";
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import TableCheckpoints from "../Tables/TableCheckpoints";
import WidgetNumberParticipants from "../Widgets/NumberParticipants"
import WidgetFasterMale from "../Widgets/FasterMale"
import WidgetFasterFemale from "../Widgets/FasterFemale"
import Loader from "../Loader/Loader";
import Tooltip from '@material-ui/core/Tooltip';
import { FormattedMessage} from 'react-intl';


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;
var AthleteListSize = 0;
var sizeCheckpointList = 0;
var arrayTotalAthlets = [];

export default class EventSubscribers extends Component {
  constructor(props) {
    super(props);
    this.state = {
      CheckpointList: [],
      loadingAthlets: false,
      loadingCheckpoints: false,
      Checkpointid: 0,
      checkpointname: "",
      checkpointnameShow: <div>< FormattedMessage id="SEARCHING" />&nbsp;&nbsp;<i class="fa fa-refresh fa-spin"/></div>,
      AtheletsTotal: 0,
      AtheletsTotalLoaded: false,
      CheckpointTotalLoaded: false,
      AthleteList: [],
      CheckpointTotal:[]
    };
    this.GetFirstRunCheckpointList();
    this.PopulateBadgesvalues()
  }



  /*for the first time when page is loaded */
  GetFirstRunCheckpointList = async e => {
    const API_CALL = await fetch(`${API_URL}/competition/Checkpoints/${this.props.match.params.CompID}/`, {
      headers: new Headers({
        Authorization: TOKEN
      })
    });
    const data = await API_CALL.json();
    this.setState({ CheckpointList: data, loadingCheckpoints: true });
    this.Getlastcheckpoint();

  };

  /*for the  time when props change */
  GetCheckpointList = async e => {
    const API_CALL = await fetch(`${API_URL}/competition/Checkpoints/${this.props.match.params.CompID}/`, {
      headers: new Headers({
        Authorization: TOKEN
      })
    });
    const data = await API_CALL.json();
    this.setState({ CheckpointList: data, loadingCheckpoints: true });
  };

  /*get list of athlets*/
  GetAthleteList = async e => {
    const API_CALL = await fetch(`${API_URL}/results/AthsTimeWithGlobalPositions/${this.props.match.params.CompID}/${this.state.Checkpointid}/`, {
      headers: new Headers({
        Authorization: TOKEN
      })
    });
    const data = await API_CALL.json();
    AthleteListSize = await data.length;
    this.setState({ AthleteList: data, loadingAthlets: true });
  };
  /*get total of athlets by checkpoint*/
  async GetTotalAthlets() {
    const API_CALL = await fetch(`${API_URL}/results/AthTotalByCheckpoint/${this.props.match.params.CompID}/`, {
      headers: new Headers({
        Authorization: TOKEN
      })
    });
    const data = await API_CALL.json()
    this.setState({ CheckpointTotal: data, CheckpointTotalLoaded: true });

  };

  /*when props change*/
  async componentDidUpdate(prevProps) {
    if (prevProps.match.params.CompID !== this.props.match.params.CompID) {
      this.setState({ loadingAthlets: false })
      await this.GetFirstRunCheckpointList();

    }
  }

  /*when props change*/
  UpdateStateCheckpoint(checkpointID, checkpointName) {
    this.setState({ Checkpointid: checkpointID, checkpointname: checkpointName, loadingAthlets: false, checkpointnameShow: checkpointName }, this.GetAthleteList);
  };

  /*get last checkpoint with values in the database*/
  async Getlastcheckpoint() {
    let checkpointID = this.state.CheckpointList.map(checkpoint => checkpoint.checkpoint.checkpoint_id)
    let checkpointName = this.state.CheckpointList.map(checkpoint => checkpoint.checkpoint.local)
    sizeCheckpointList = checkpointID.length - 1; //-1 because array start in 0
    this.setState({ Checkpointid: checkpointID[sizeCheckpointList], checkpointname: checkpointName[sizeCheckpointList] });
    await this.GetAthleteList();

    //Search for the last checkpoint with times inserted
    if (AthleteListSize === 0) {
      while (sizeCheckpointList >= 0) {
        sizeCheckpointList = sizeCheckpointList - 1;
        this.setState({ Checkpointid: checkpointID[sizeCheckpointList], checkpointname: checkpointName[sizeCheckpointList] });
        await this.GetAthleteList();
        if (AthleteListSize > 0) {
          this.setState({ checkpointnameShow: this.state.checkpointname })
          break;
        }
        else {
          //if there is no times
          if (sizeCheckpointList === 0) {
            this.setState({ Checkpointid: checkpointID[0], checkpointname: checkpointName[0] });
            this.setState({ checkpointnameShow: this.state.checkpointname })
            await this.GetAthleteList();
            break;
          }
        }


      }
    }
    else {
      this.setState({ checkpointnameShow: this.state.checkpointname })
    }

  }

  PopulateBadgesvalues() {
      this.GetTotalAthlets()
  }

  GetBadgesvalues(checkpoint) {
    var total = 0;
    this.state.CheckpointTotal.map(checkpointValue => { if (checkpointValue.CheckpointID === checkpoint) { total =checkpointValue.Value } })
    return (total);
  }


  CheckpointList(checkpoint_order, length) {
    if (checkpoint_order === '99'){
      return (<span style={{ padding: '1px' }} class="fa fa-fw fa-flag-checkered" ></span>)}
    else if (checkpoint_order === '00'){
      return (<span style={{ padding: '1px' }} class="fa fa-fw fa-bell-o" ></span>)} 
    else if(Math.ceil(length/2)-1=== parseInt(checkpoint_order)){

      return (<span style={{ padding: '1px' }} class="fa fa-fw fa-map-marker" ></span>)} 
    else
    {        return (<span style={{ padding: '1px' }}>{checkpoint_order}</span>)} 
    }
    
  

  /*Loading message for the checkpoints*/
  LoadingCheckpoints() {
    if (this.state.loadingCheckpoints === false) {
      return (
        <th key="Loading"> <a href="##">< FormattedMessage id="LOADING" /><i class="fa fa-refresh fa-spin"/></a></th>
      )
    }
    else
      return (
        this.state.CheckpointList.map(checkpoint => {
          if (checkpoint.checkpoint.checkpoint_id === this.state.Checkpointid) {
            return (

              <th key={checkpoint.checkpoint.checkpoint_id} >
                <Tooltip placement="top" title={<span style={{ fontSize: "16px" }}>{checkpoint.checkpoint.local}</span>} arrow>
                  <a style={{ color: '#37474F' }} onClick={() => this.UpdateStateCheckpoint(checkpoint.checkpoint.checkpoint_id, checkpoint.checkpoint.local)} href="##">
                   {this.CheckpointList(checkpoint.checkpoint_order,this.state.CheckpointList.length)}
                    <span class="badge bg-red">{this.GetBadgesvalues(checkpoint.checkpoint.checkpoint_id)}</span>

                  </a>
                </Tooltip>


              </th>
            )
          }
          else {
            return (
              <th key={checkpoint.checkpoint.checkpoint_id} >
                <Tooltip placement="top" title={<span style={{ fontSize: "16px" }}>{checkpoint.checkpoint.local}</span>} arrow>
                  <a onClick={() => this.UpdateStateCheckpoint(checkpoint.checkpoint.checkpoint_id, checkpoint.checkpoint.local)} href="##">
                  {this.CheckpointList(checkpoint.checkpoint_order,this.state.CheckpointList.length)}
                   <span class="badge bg-red">{this.GetBadgesvalues(checkpoint.checkpoint.checkpoint_id)}</span>
                  </a>
                </Tooltip>

              </th>

            )
          }



        }))
  }

  /*Loading message for the athlete*/
  LoadingAthletes() {
    if (this.state.loadingAthlets === false) {
      return (
        <section className="content">
          <div className="row">
            <div className="col-xs-12">
              <div className="box">
                {/* /.box-body */}
                <div className="box-body" style={{ display: 'flex', justifyContent: 'center' }}>
                  <Loader />
                </div>

              </div>
            </div>
          </div>
        </section>
      )
    }
    else {
      return (
        <section className="content">
          <div className="row">
            {/* widget for the number of participants */}
            <WidgetNumberParticipants athleteList={this.state.AthleteList} />
            {/* widget for the Faster */}
            <WidgetFasterMale athleteList={this.state.AthleteList} />
            <WidgetFasterFemale athleteList={this.state.AthleteList} />


          </div>
          <div
            style={{
              padding: "10px 30px",
              background: "#3c8dbc",
              zIndex: 999999,
              fontSize: 16,
              fontWeight: 600
            }}
          >

            <table className="text-center" style={{ padding: "5px", border: "5px", width: "100%" }}>
              <tbody>
                <tr style={{ textAlign: "center !important" }} >
                  {this.LoadingCheckpoints()} {/* load checkpoint */}
                </tr>
              </tbody>
            </table>


          </div>
          <div className="row">
            <div className="col-xs-12">
              <div className="box box-primary">
                {/* /.box-body */}
                <div className="box-body"></div>
                <TableCheckpoints
                  athleteList={this.state.AthleteList}
                  eventID={this.props.match.params.EventID}
                  competitionID={this.props.match.params.CompID}
                  loading={this.state.loading}
                />

              </div>
            </div>
            {/* /.col */}
          </div>
          {/* /.row */}
        </section>
      )
    }
  }

  render() {
    
    return (
      <div>
        <Header eventID={this.props.match.params.EventID} />
        {/*Send the event id to the MENU, to get the competition list*/}
        <Menu eventID={this.props.match.params.EventID} competitionID={this.props.match.params.CompID} />
        {/*CONTENT*/}
        <div>
          {/* Content Wrapper. Contains page content */}
          <div className="content-wrapper">

            {/* Content Header (Page header) */}
            <section className="content-header">
              <h1>
                <span style={{ float: "right" }}  > {this.state.checkpointnameShow}</span>

                <span >{this.props.match.params.CompName}</span>
              </h1>
            </section>


            {/* Main content */}
            {/*Check if data is loading*/}
            {this.LoadingAthletes()} {/* loaded athletes */}
          </div>
          {/* /.content-wrapper */}
        </div>
        <Footer eventID={this.props.match.params.EventID} />
      </div>
    );
  }
}
