import React, { Component } from "react";
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import TableSubscribers from "../Tables/TableParticipants";
import WidgetNumberParticipants from "../Widgets/NumberParticipants";
import WidgetNumberCategories from "../Widgets/NumberCategories";
import WidgetNumberteams from "../Widgets/NumberTeams";
import WidgetNumberNacionalities from "../Widgets/NumberNationalities";
import Loader from "../Loader/Loader";
import { FormattedMessage} from 'react-intl';


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;
var EventID = 0;

export default class EventSubscribers extends Component {
  constructor(props) {
    super(props);
    this.state = {
      athleteList: [],
      loading: false,
    };

    this.getAthletsList();


  }


  /*List of athlets*/
  getAthletsList = async e => {
    EventID = this.props.match.params.EventID;
    const API_CALL = await fetch(`${API_URL}/trail/Athletes/${EventID}/`, {
      headers: new Headers({
        Authorization: TOKEN
      })
    });
    const data = await API_CALL.json();
    this.setState({ athleteList: data, loading: true });
  };



  render() {
    return (
      <div>
        <Header eventID={EventID}  />
        {/*Send the event id to the MENU, to get the competition list*/}
        <Menu eventID={EventID} competitionID={0} />
        {/*CONTENT*/}
        <div>
          {/* Content Wrapper. Contains page content */}
          <div className="content-wrapper">

            {/* Content Header (Page header) */}
            <section className="content-header">

            </section>
            {/* Main content */}
            {/*Check if data is loading*/}
            {(() => {
              if (this.state.loading === false) {
                return (
                  <section className="content">
                    <div className="row">
                      <div className="col-xs-12">
                        <div className="box">
                          {/* /.box-body */}
                          <div className="box-body" style={{ display: 'flex', justifyContent: 'center' }}>
                            <Loader />
                          </div>

                        </div>
                      </div>
                    </div>
                  </section>
                )
              }
              else {
                return (
                  <section className="content">
                    <div className="row">
                      {/* widget for the number of participants */}
                      <WidgetNumberParticipants athleteList={this.state.athleteList} />
                      {/* widget for the number of Teams */}
                      <WidgetNumberteams athleteList={this.state.athleteList} />
                      {/* widget for the number of Categories */}
                      <WidgetNumberCategories athleteList={this.state.athleteList} />
                      {/* widget for the number of Nacionalities */}
                      <WidgetNumberNacionalities athleteList={this.state.athleteList} />
                    </div>
                    <div className="row">
                      <div className="col-xs-12">
                        <div className="box">
                          {/* /.box-body */}
                          <div className="box box-primary"></div>
                          <div style={{ width: 100 + '%!important' }}>
                            <TableSubscribers
                              athleteList={this.state.athleteList}
                              loading={this.state.loading}
                              eventID={EventID}
                              competitionID={0}
                            />
                          </div>
                          {/* Table for the list of participants */}


                        </div>
                      </div>
                      {/* /.col */}
                    </div>
                    {/* /.row */}

                  </section>


                )
              }
            })()}
          </div>
          {/* /.content-wrapper */}
        </div>
        <Footer eventID={EventID} />
      </div>
    );
  }

}
