import React, { Component } from 'react'
import { Pie  } from 'react-chartjs-2';

var options;
export default class pie extends Component {


    render() {
        const data = {

            labels: this.props.data.map(items => { return (items.Label) }),
            datasets: [
                {
                    fill: false,
                    lineTension: 0.1,
                    backgroundColor: ["#64B5F6","#9575CD","#EF9A9A", "#B0BEC5", "#F48FB1", "#EEEEEE", "#B39DDB", "#BCAAA4", "#9FA8DA", "#FFAB91", "#90CAF9", "#FFCC80", "#C5E1A5", "#81D4FA", "#FFF59D", "#FFE082", "#80CBC4", "#E6EE9C", "#80DEEA",
                    "#C62828", "#37474F", "#AD1457", "#424242", "#6A1B9A", "#4E342E", "#4527A0", "#D84315", "#283593", "#1565C0", "#EF6C00", "#0277BD", "#FF8F00", "#00838F", "#F9A825", "#00695C", "#9E9D24",
                    "#2E7D32", "#558B2F", "#F44336", "#607D8B", "#E91E63", "#795548", "#9C27B0", "#795548", "#673AB7", "#FF5722", "#FF9800", "#2196F3", "#FF9800", "#03A9F4", "#FFC107", "#00BCD4", "#FFEB3B", "#009688",
                    "#4CAF50","#CDDC39","#009688","#00BCD4","#CDDC39","#EF9A9A", "#B0BEC5", "#F48FB1", "#EEEEEE", "#B39DDB", "#BCAAA4", "#9FA8DA", "#FFAB91", "#90CAF9", "#FFCC80", "#C5E1A5", "#81D4FA", "#FFF59D", "#FFE082", "#80CBC4", "#E6EE9C", "#80DEEA",
                    "#C62828", "#37474F", "#AD1457", "#424242", "#6A1B9A", "#4E342E", "#4527A0", "#D84315", "#283593", "#1565C0", "#EF6C00", "#0277BD", "#FF8F00", "#00838F", "#F9A825", "#00695C", "#9E9D24",
                    "#2E7D32", "#558B2F", "#F44336", "#607D8B", "#E91E63", "#795548", "#9C27B0", "#795548", "#673AB7", "#FF5722", "#FF9800", "#2196F3", "#FF9800", "#03A9F4", "#FFC107", "#00BCD4", "#FFEB3B", "#009688",
                    "#4CAF50","#CDDC39","#009688","#00BCD4","#CDDC39"],
                    pointBorderColor: '#455A64',
                    pointBackgroundColor: '#455A64',
                    pointBorderWidth: 1,
                    pointHoverRadius: 5,
                    pointHoverBackgroundColor: '#455A64',
                    pointHoverBorderColor: '#455A64',
                    pointHoverBorderWidth: 2,
                    pointRadius: 1,
                    pointHitRadius: 10,
                    data: this.props.data.map(items => { return (items.Value) })
                }]
        }

        options = {
            legend: {
                display: true,
                position: 'bottom',
            },
        };

        return (
            <div>
                <Pie  data={data} options={options} />
            </div>
        )
    }
}
