import React, { Component } from "react";
import ReactTable from "react-table";
import {FormatTime} from "../Scripts/time"
import { calculatePagination } from "../Scripts/PaginationHelper"
import { FormattedMessage } from 'react-intl';

var size = window.innerWidth;


export default class TableTier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: this.props.loading
    };
  }

  CheckSize() {
    if (size > 800) {
      return true;
    }
    else {
      return false;
    }}
    checkPosition(position){
      if (0=== parseInt(position))
      {
        return("-")
      }
      else
      {
        return(position)
  
      }
    }

  CalculateKMperH(checkpointName,time){
    var distance
    this.props.checkpoint.map(checkpoint =>{
      if(checkpoint.checkpoint.local===checkpointName){
        distance=checkpoint.distancia_acumulada
      }
      return null
    })
    var km=distance/1000;
    var TimeSplit= time.split(":")
    var H=TimeSplit[0]
    var M=TimeSplit[1]
    var S=TimeSplit[2]
    var finaltime=parseFloat(H)+parseFloat((M/60))+parseFloat((S/3600))
    var KMperH=parseFloat(km)/parseFloat(finaltime)
    if(isNaN(KMperH))
    {
      KMperH=0;
    }
  return(KMperH.toFixed(2))
  }

  CalculateMINperKM(checkpointName,time){
    var MInperKM = parseFloat(60/this.CalculateKMperH(checkpointName,time))
    if(!isFinite(MInperKM))
    {
      MInperKM=0;
    }
  return(MInperKM.toFixed(2));


  }

  getDistance(checkpointName){
    var distance

    this.props.checkpoint.map(checkpoint =>{
      if(checkpoint.checkpoint.local===checkpointName){
        distance=checkpoint.distancia_acumulada
      }
      return null
    })
    distance=parseFloat(distance/1000);
    return(distance.toFixed(2)+" km")

  }
  getaltitude(checkpointName){
    var Altitude

    this.props.checkpoint.map(checkpoint =>{
      if(checkpoint.checkpoint.local===checkpointName){
        Altitude=checkpoint.checkpoint.altitude
      }
      return null
    })
    return(Altitude+" m")

  }
  render() {
    //Info for the filters


    //Table
    const columns = [
      {
        Header: "Id",
        accessor: "athlete_id",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        show: false
      },
      {
        Header: (size>800)?<FormattedMessage id="POSITION" />:"POS",
        width:(size>800)?100:50,

        accessor: "PositionTier",
        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <span> {this.checkPosition(row.value)} </span>
        ),

      },
      {
        Header: <FormattedMessage id="PLACE" />,
        accessor: "local",
        filterable: false,
        sortable: false,

        style: {
          textAlign: "Center"
        },
        Cell: row => row.value

      },
      {
        Header: <FormattedMessage id="TIME" />,
        accessor: "acumulated_time",
        filterable: false,
        sortable: false,

        style: {
          textAlign: "Center"
        },
        Cell: row => FormatTime(row.value)

      },
      {
        Header:  "Dif 1" ,
        accessor: "dif",
        filterable: false,
        sortable: false,

        style: {
          textAlign: "Center"
        },
        Cell: row => FormatTime(row.value)
      },
      {
        Header: <FormattedMessage id="HEIGHT" />,
        filterable: false,
        sortable: false,
        show : this.CheckSize(),

        style: {
          textAlign: "Center"
        },
        Cell: row => this.getaltitude(row.original.local)
      },
      {
        Header: <FormattedMessage id="DISTANCE" />,
        filterable: false,
        sortable: false,
        show : this.CheckSize(),


        style: {
          textAlign: "Center"
        },
        Cell: row => this.getDistance(row.original.local)
      },
      {
        Header: "KM/H",
        filterable: false,
        sortable: false,
        width:(size>800)?100:70,

        style: {
          textAlign: "Center"
        },
        Cell: row => this.CalculateKMperH(row.original.local,row.original.acumulated_time)
      },
      {
        Header: "MIN/KM",
        filterable: false,
        sortable: false,
        width:(size>800)?100:70,

        style: {
          textAlign: "Center"
        },
        Cell: row => this.CalculateMINperKM(row.original.local,row.original.acumulated_time)
      }
    ];
    return (
      <ReactTable
        columns={columns}
        data={this.props.athleteList}
        filterable={false}
        noDataText={<FormattedMessage id="NODATA" />}
        defaultPageSize={parseInt(this.props.athleteList.length)}
        showPagination={false}
        showPaginationTop={false}
        showPaginationBottom={false}
        showPageSizeOptions={false}
        showPageJump={false}
        collapseOnSortingChange={true}
        collapseOnPageChange={true}
        collapseOnDataChange={true}
        freezeWhenExpanded={false}
        sortable={false}
        multiSort={false}
        resizable={true}
        defaultSortDesc={false}
        pageText={<FormattedMessage id="PAGE" />}
        rowsText={""}
        className="-striped -highlight"
      />
    );
  }
}
