import React, { Component } from "react";
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import TableWinners from "../Tables/TableWinners";
import WidgetFaster from "../Widgets/Faster"
import Loader from "../Loader/Loader";
import { FormattedMessage} from 'react-intl';


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;
var size = window.innerWidth;

export default class Winners extends Component {
    constructor(props) {
        super(props);
        this.state = {
            CategoryList: [],
            loadingCategory: false,
            loadingAthlete: false,
            CategoryName: "Global",
            Positon: "PositionGlobal",
            Gender: "M",
            AthleteList: []
        };
        this.GetCategoryList();
        this.GetCategoryList = this.GetCategoryList.bind(this);

    }

    StaticElementGlobal() {
        if (this.state.CategoryName === "Global") {
            return (<th key="Global" > <a style={{ color: '#37474F' }} onClick={() => this.UpdateStateCheckpoint("Global")} href="##"><FormattedMessage id="GERAL" /></a></th>
            )
        }
        else {
            return (
                <th key="Global" > <a onClick={() => this.UpdateStateCheckpoint("Global")} href="##"><FormattedMessage id="GERAL" /></a></th>
            )

        }
    }

    StaticElementGlobalM() {
        if (this.state.CategoryName === "Global M") {
            return (<th key="GlobalM" > <a style={{ color: '#37474F' }} onClick={() => this.UpdateStateCheckpoint("Global M")} href="##">{(size>800)? <FormattedMessage id="MEN" />: <i className="fa fa-fw fa-male" /> }</a></th>
            )
        }
        else {
            return (
                <th key="GlobalM" > <a onClick={() => this.UpdateStateCheckpoint("Global M")} href="##">{(size>800)? <FormattedMessage id="MEN" /> : <i className="fa fa-fw fa-male" /> }</a></th>
            )

        }
    }
    StaticElementGlobalF() {
        if (this.state.CategoryName === "Global F") {
            return (

                <th key="GlobalF" > <a style={{ color: '#37474F' }} onClick={() => this.UpdateStateCheckpoint("Global F")} href="##">{(size>800)? <FormattedMessage id="WOMEN" /> : <i className="fa fa-fw fa-female" /> }</a></th>
            )
        }
        else {
            return (

                <th key="GlobalF" > <a onClick={() => this.UpdateStateCheckpoint("Global F")} href="##">{(size>800)? <FormattedMessage id="WOMEN" /> : <i className="fa fa-fw fa-female" /> }</a></th>
            )

        }
    }
    /*get Category for the winners*/
    GetCategoryList = async e => {
        const API_CALL = await fetch(`${API_URL}/results/AthsFinalTimeGlobalWithPositions/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        const dataSlice = data.slice(0, 3); //Get the 3 winners for the podio

        //get the category
        let category = data.map(athelete => {
            return athelete.escalao;
        });
        //remove duplicates
        const Tableuniquecategory = [...new Set(category)];
        Tableuniquecategory.sort();
        this.setState({ CategoryList: Tableuniquecategory, AthleteList: dataSlice,  loadingAthlete: true, loadingCategory: true });
    };
    /*get Global winners*/
    GetAthleteListGender = async e => {
        const API_CALL = await fetch(`${API_URL}/results/AthsFinalTimeGenderWithPositions/${this.props.match.params.CompID}/${this.state.Gender}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        const dataSlice = data.slice(0, 3); //Get the 3 winners for the podio
        this.setState({ AthleteList: dataSlice, loadingAthlete: true });
    };
    /*get Category winners*/
    GetAthleteListCategory = async e => {
        const API_CALL = await fetch(`${API_URL}/results/AthsFinalTimeTierWithPositions/${this.props.match.params.CompID}/${this.state.CategoryName}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        const dataSlice = data.slice(0, 3); //Get the 3 winners for the podio
        this.setState({ AthleteList: dataSlice, loadingAthlete: true });
    };
    /*props change*/
    componentDidUpdate(prevProps) {
        if (prevProps.match.params.CompID !== this.props.match.params.CompID) {
            this.setState({ Positon: "PositionGlobal", loadingAthlete: false });
            this.GetCategoryList();
            this.UpdateStateCheckpoint("Global");
        }
    }
    /*Check if is global or tier*/
    async UpdateStateCheckpoint(Category) {
        if( Category === "Global")
        {
            this.setState({ CategoryName: Category, Positon: "PositionGlobal", loadingAthlete: false }, this.GetCategoryList);
        }
        else if (Category === "Global M") {
            this.setState({ CategoryName: Category, Positon: "PositionGender", Gender: "M", loadingAthlete: false }, this.GetAthleteListGender);
        }
        else if (Category === "Global F") {
            this.setState({ CategoryName: Category, Positon: "PositionGender", Gender: "F", loadingAthlete: false }, this.GetAthleteListGender);
        }
        else {
            this.setState({ CategoryName: Category, Positon: "PositionTier", loadingAthlete: false }, this.GetAthleteListCategory);
        }
    };
    /*Loadings*/
    LoadingAthlete() {
        if (this.state.loadingAthlete === false) {
            return (
                <section className="content">
                    <div className="row">
                        <div className="col-xs-12">
                            <div className="box">
                                {/* /.box-body */}
                                <div className="box-body" style={{ display: 'flex', justifyContent: 'center' }}>
                                    <Loader />
                                </div>

                            </div>
                        </div>
                    </div>
                </section>
            )

        }
        else {
            return (
                <section className="content">
                    <div className="row">
                        {/* widget for the Faster */}
                        <WidgetFaster athleteList={this.state.AthleteList} />

                    </div>
                    <div
                            style={{
                                padding: "10px 30px",
                                background: "#3c8dbc",
                                zIndex: 999999,
                                fontSize: 16,
                                fontWeight: 600
                            }}
                        >
                            <table className="text-center"   style={{padding:"5px", border:"5px", width:"100%"}}>
                                {this.LoadingCategory()}
                            </table>


                        </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <div className="box box-primary">
                                {/* /.box-body */}
                                <div className="box-body"></div>
                                <TableWinners
                                    eventID={this.props.match.params.EventID}
                                    competitionID={this.props.match.params.CompID}
                                    athleteList={this.state.AthleteList}
                                    loading={this.state.loading}
                                    position={this.state.Positon}
                                />

                            </div>
                        </div>
                        {/* /.col */}
                    </div>
                    {/* /.row */}
                </section>

            )

        }

    }

    LoadingCategory() {
        if (this.state.loadingCategory === false) {
            return (
                <th> <a href="##"><FormattedMessage id="LOADING" /> <i class="fa fa-refresh fa-spin"></i></a></th>
            )
        }
        else {
            return (
                <tbody>
                    <tr>
                                               
                        {this.StaticElementGlobal()}
                        {this.StaticElementGlobalM()}
                        {this.StaticElementGlobalF()}
                        {this.state.CategoryList.map(category => {
                            if (this.state.CategoryName === category) {
                                return (
                                    <th key={category} > <a style={{ color: '#37474F' }} onClick={() => this.UpdateStateCheckpoint(category)} href="##">{category}</a></th>
                                )
                            }
                            else {
                                return (
                                    <th key={category} > <a onClick={() => this.UpdateStateCheckpoint(category)} href="##">{category}</a></th>
                                )
                            }

                        })}
                    </tr>
                </tbody>

            )

        }

    }
    render() {
        return (
            <div>
                <Header eventID={this.props.match.params.EventID} />
                {/*Send the event id to the MENU, to get the competition list*/}
                <Menu eventID={this.props.match.params.EventID} competitionID={this.props.match.params.CompID} />
                {/*CONTENT*/}
                <div>
                    {/* Content Wrapper. Contains page content */}
                    <div className="content-wrapper">
                        {/* Content Header (Page header) */}
                        <section className="content-header">
                            <h1>
                                {/* <span style={{ float: "right" }}> {this.state.CategoryName} </span>*/}
                                <span> {this.props.match.params.CompName} </span>
                            </h1>
                        </section>
                        {/* Main content */}
                        {this.LoadingAthlete()}
                    </div>
                    {/* /.content-wrapper */}
                </div>
                <Footer eventID={this.props.match.params.EventID} />
            </div>
        )
    }
}
