import React, { Component } from 'react'
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import TableTeams from "../Tables/TableTeams";
import Loader from "../Loader/Loader";
import { FormatTime } from "../Scripts/time"
import { FormattedMessage} from 'react-intl';


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;

//Control Variable
var firstrun = 0;

export default class Teams extends Component {

    constructor(props) {
        super(props);
        this.state = {
            teamlist: [],
            loading: false,
            loadingCheckpoint: false,
            genderState: "MALE",
            checkpointid: 0

        };
        this.GetLastCheckpoint();
        this.GetLastCheckpoint = this.GetLastCheckpoint.bind(this);

    }

    GetLastCheckpoint = async () => {
        const API_CALL = await fetch(`${API_URL}/competition/Checkpoints/${this.props.match.params.CompID}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        //dont use the ===, dont work for some reason
        data.map(checkpoint => { if (checkpoint.checkpoint_order == 99) { this.setState({ loadingCheckpoint: true, checkpointid: checkpoint.checkpoint.checkpoint_id }) } })
    };
    GetTeamsMale = async () => {
        if (this.state.loadingCheckpoint === true) {
            const API_CALL = await fetch(`${API_URL}/results/teamresultsMALE/${this.props.match.params.CompID}/${this.state.checkpointid}/`, {
                headers: new Headers({
                    Authorization: TOKEN
                })
            });
            const data = await API_CALL.json();
            this.setState({ teamlist: data, loading: true })
        }
    };

    GetTeamsFemale = async () => {
        const API_CALL = await fetch(`${API_URL}/results/teamresultsFEMALE/${this.props.match.params.CompID}/${this.state.checkpointid}/`, {
            headers: new Headers({
                Authorization: TOKEN
            })
        });
        const data = await API_CALL.json();
        this.setState({ teamlist: data, loading: true })
    };

    UpdateStateGender(gender) {

        if (this.state.loadingCheckpoint === true) {
            if (gender === "MALE") {
                this.setState({ genderState: gender, loading: false }, this.GetTeamsMale)
            }
            else {
                this.setState({ genderState: gender, loading: false }, this.GetTeamsFemale)
            }
        }


    }
    /*when props change*/
    componentDidUpdate(prevProps) {
        if (prevProps.match.params.CompID !== this.props.match.params.CompID) {
            this.GetLastCheckpoint();
            this.UpdateStateGender("MALE");

        }
    }


    checkIFthereISDataTOShow() {
        if (this.state.teamlist.length > 0) {
            return (
                this.state.teamlist.map(team => {
                    var APItime = team.Time.split(":")
                    var timeLimited = APItime[0] + ":" + APItime[1] + ":" + APItime[2].substring(0, 2)
                    return (
                        <div className="col-md-4">
                            {/* general form elements */}
                            <div className="box box-primary">
                                <div className="box-header with-border">
                                    <div style={{ textAlign: "center" }} className="col-md-2 invoice-col">
                                     <a  class="btn btn-app" style={{  pointerEvents:"none",fontSize: "23px"}}> <strong>{team.Position}</strong> </a>
                                    </div>
                                    <div style={{ textAlign: "center" }} className="col-md-10 invoice-col">
                                        <h5> 
                                            <span style={{fontSize:"20px"}}>{team.Team.substring(0,70)}</span>
                                            <br></br>
                                        </h5>

                                    </div>
                                </div>
                                {/* /.box-header */}
                                {/* form start */}
                                <div className="box-body">
                                    <div style={{ textAlign: "center" }} className="col-md-4 invoice-col">
                                        <img className="profile-user-img img-responsive img-circle" src={team.LogoURL} alt="User profile picture" />
                                        <span style={{ fontSize: "20px" }}>
                                        < FormattedMessage id="TIMECAPS" />: </span>
                                        <span style={{ fontSize: "20px" }}>
                                            <strong>{FormatTime(timeLimited)}</strong> </span>
                                    </div>
                                    <div style={{ textAlign: "center" }} className="col-md-8 invoice-col">
                                        <TableTeams eventID={this.props.match.params.EventID} compID={this.props.match.params.CompID} athleteList={team.Athlets} />
                                    </div>
                                </div>
                                {/* /.box-body */}

                            </div>
                            {/* /.box */}

                        </div>

                    )
                })
            )

        }
        else {
            return (
                <div className="col-md-12">
                    {/* general form elements */}
                    <div className="box box-primary">

                        {/* /.box-header */}
                        {/* form start */}
                        <form role="form">
                            <div className="box-body">
                                <p>< FormattedMessage id="MESSAGETEAMSDATA" /></p>

                            </div>
                            {/* /.box-body */}

                        </form>
                    </div>
                    {/* /.box */}

                </div>

            )
        }

    }

    StaticElementTeamM() {
        if (this.state.genderState === "MALE") {

            return (
                <th key="MALE" > <a style={{ color: '#37474F' }} onClick={() => this.UpdateStateGender("MALE")} href="#">< FormattedMessage id="MEN" /><i class="fa fa-fw fa-male"></i></a></th>
            )
        }
        else {
            return (
                <th key="MALE" > <a style={{ color: 'white' }} onClick={() => this.UpdateStateGender("MALE")} href="#">< FormattedMessage id="MEN" /><i class="fa fa-fw fa-male"></i></a></th>
            )

        }
    }

    StaticElementTeamF() {
        if (this.state.genderState === "FEMALE") {
            return (

                <th key="FEMALE" > <a style={{ color: '#37474F' }} onClick={() => this.UpdateStateGender("FEMALE")} href="#">< FormattedMessage id="WOMEN" /><i class="fa fa-fw fa-female"></i></a></th>
            )
        }
        else {
            return (

                <th key="FEMALE" > <a style={{ color: 'white' }} onClick={() => this.UpdateStateGender("FEMALE")} href="#">< FormattedMessage id="WOMEN" /><i class="fa fa-fw fa-female"></i></a></th>
            )

        }
    }

    render() {
        if (this.state.loadingCheckpoint === true) {
            if (firstrun === 0) {
                this.GetTeamsMale()
                firstrun = 1;
            }
        }

        return (

            <div>
                <Header eventID={this.props.match.params.EventID} />
                {/*Send the event id to the MENU, to get the competition list*/}
                <Menu eventID={this.props.match.params.EventID} competitionID={this.props.match.params.CompID} />
                {/* Content Wrapper. Contains page content */}

                <div className="content-wrapper">

                    <div
                        style={{
                            paddingTop: "30px",
                            background: "#367fa9",
                            zIndex: 999999,
                            fontSize: 16,
                            fontWeight: 600
                        }}
                    >
                        <table className="text-center" style={{ width: "100%", borderSpacing: "10px", borderCollapse: "separate" }}>
                            <tbody>
                                <tr >
                                    {this.StaticElementTeamM()}
                                    {this.StaticElementTeamF()}
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <section className="content-header">
                        <h1>
                            <span style={{ float: "right" }}>{(this.state.genderState == "MALE") ? < FormattedMessage id="MEN" /> : < FormattedMessage id="WOMEN" />}</span>

                            <span >{this.props.match.params.CompName}</span>
                        </h1>
                    </section>
                    {(() => {
                        if (this.state.loading === false) {
                            return (
                                <section className="content">
                                    <div className="row">
                                        <div className="col-xs-12">
                                            <div className="box">
                                                {/* /.box-body */}
                                                <div className="box-body" style={{ display: 'flex', justifyContent: 'center' }}>
                                                    <Loader />
                                                </div>

                                            </div>
                                        </div>
                                    </div>
                                </section>
                            )
                        }
                        else {
                            return (
                                <section className="content">
                                    <div className="row">
                                        {/* left column */}
                                        {this.checkIFthereISDataTOShow()}
                                    </div>
                                    {/* /.row */}
                                </section>
                            )



                        }
                    })()}
                </div>
                <Footer eventID={this.props.match.params.EventID} />

            </div>
        )
    }
}