import React from "react";
import { Link } from "react-router-dom";
import { FormattedMessage} from 'react-intl';

const Trails = props => (
  
  <div className="container-fluid" >
    <div className="row">
      {props.trails.map(trail => {

        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
        
        var date = yyyy + '-' + mm + '-' + dd;

        var hours   =  today.getHours();
        var minutes = today.getMinutes();
        var seconds = today.getSeconds();
        if (hours   < 10) {hours   = "0"+hours;}
        if (minutes < 10) {minutes = "0"+minutes;}
        if (seconds < 10) {seconds = "0"+seconds;}
        
        var time = hours+':'+minutes+':'+seconds;
        var dateTime = date + "T" + time;
        return (
          <div
            key={trail.trail_event_id}
            className="col-md-4"
            style={{ marginBottom: "2rem" }}
          >
            {/* link is to redirect usando acento `*/}
            <Link to={{pathname: `/EventParticipants/${trail.trail_event_id}`}}>
              <div className="trails__box">
                <img style={{  width: "100%", height: "100%"}}
                  className="trails__box-img"
                  src={trail.imageHome_url}
                  alt={trail.name}
                ></img>
                <div className="trail__text">
                  <h5 className="trail__title">{trail.name}</h5>
                  <p className="trail__subtitle">
                    {/* if */}
                    {trail.init_date < dateTime && trail.end_date > dateTime  ? (
                      /* then */ <span style={{ color: "#F44336" }} >
                        a decorrer
                      </span>
                    ) : (
                      /* else */ <span style={{color:"#3c8dbc"}}>
                        <FormattedMessage id="DATE" />:{trail.init_date.substring(0, 10)} <FormattedMessage id="HOUR" />:
                        {trail.init_date.substring(11, 16)}
                      </span>
                    )}
                  </p>
                </div>
              </div>
            </Link>
          </div>
        );
      })}
    </div>
  </div>
);
export default Trails;
