import React, { Component } from "react";
import ReactTable from "react-table";
import "react-table/react-table.css";
import { Link } from "react-router-dom";
import Favorites from "../favorites/favoritesChecker"
import { FormattedMessage } from 'react-intl';
import { calculatePagination } from "../Scripts/PaginationHelper"
import { minimalSize } from "../Scripts/PaginationHelper"


var size = window.innerWidth;


export default class TableSubscribers extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: this.props.loading
    };
  }

  CheckSize() {
    if (size > 800) {
      return true;
    }
    else {
      return false;
    }
  }

  addFavorites() {
    localStorage.setItem("ListaFavoritos", this.props.ListaFavoritos)
  }
  render() {
    //Info for the filters
    //get the category
    let category = this.props.athleteList.map(athelete => {
      return athelete.category;
    });
    //remove duplicates
    const Tableuniquecategory = [...new Set(category.sort())];
    //get the nacionality
    let nacionality = this.props.athleteList.map(athelete => {
      return athelete.athlete.nacionality;
    });
    //remove duplicates
    const Tableuniquenacionality = [...new Set(nacionality.sort())];
    //get the Club
    let club = this.props.athleteList.map(athelete => {
      return athelete.club;
    });
    //remove duplicates
    const Tableuniqueclub = [...new Set(club.sort())];
    //get the inscription_status_code
    let inscription_status_code = this.props.athleteList.map(athelete => {
      return athelete.inscription_status_code;
    });
    //remove duplicates
    const Tableuniqueinscription_status_code = [
      ...new Set(inscription_status_code.sort())
    ];

    //Table
    const columns = [
      {
        Header: "Id",
        accessor: "athlete.athlete_id",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        width: 100,
        maxWidth: 100,
        mimWidth: 100,
        show: false
      },
      {
        Header: (size > 800) ? <FormattedMessage id="DORSAL" /> : "PTL",
        id: "dorsal_number",
        accessor: d => Number(d.dorsal_number),
        style: {
          textAlign: "Center"
        },
        width: (size > 800) ? 100 : 60,
        Filter: ({ filter, onChange }) => (
          <FormattedMessage id="SEARCH" >
            {txt => <input
              onChange={event => onChange(event.target.value)}
              value={filter ? filter.value : ""}
              placeholder={txt}
              style={{
                width: "100%",
                textAlign: "Center"
              }}
            />}
          </FormattedMessage>
        ),
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete.athlete_id}` }}
          >
            <span style={{ color: "#3c8dbc" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: <FormattedMessage id="NAME" />,
        accessor: "athlete.name",
        style: {
          textAlign: "Center"
        },
        Filter: ({ filter, onChange }) => (
          <FormattedMessage id="SEARCH" >
            {txt => <input
              onChange={event => onChange(event.target.value)}
              value={filter ? filter.value : ""}
              placeholder={txt}
              style={{
                width: "100%",
                textAlign: "Center"
              }}
            />}
          </FormattedMessage>
        ),
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete.athlete_id}` }}
          >
            <span style={{ color: "#3c8dbc" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: <FormattedMessage id="TEAM" />,
        show: this.CheckSize(),

        accessor: "club",
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniqueclub.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast: "center" }}
            value={filter ? filter.value : "all"}
          >
            <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniqueclub.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: <FormattedMessage id="CATEGORY" />,
        accessor: "category",
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquecategory.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast: "center" }}
            value={filter ? filter.value : "all"}
          >
            <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniquecategory.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: <FormattedMessage id="COUNTRY" />,
        show: this.CheckSize(),

        accessor: "athlete.nacionality",
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquenacionality.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast: "center" }}
            value={filter ? filter.value : "all"}
          >
             <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniquenacionality.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: <FormattedMessage id="STATUS" />,
        accessor: "inscription_status_code",
        style: {
          textAlign: "Center"
        },
        Cell: row => {
          if (row.value === "FIN") {
            return <FormattedMessage id="FIN" />;
          } else if (row.value === "EP") {
            return <FormattedMessage id="EP" />;
          } else if (row.value === "DNS") {
            return <FormattedMessage id="DNS" />;
          } else if (row.value === "D") {
            return <FormattedMessage id="D" />;
          } else if (row.value === "DNF") {
            return <FormattedMessage id="DNF" />;
          } else if (row.value === "DSQ") {
            return <FormattedMessage id="DSQ" />;
          }

        },
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniqueinscription_status_code.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%", textAlignLast: "center" }}
            value={filter ? filter.value : "all"}
          >
            <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniqueinscription_status_code.map(k => {
              return (
                <FormattedMessage id={k.toString()}>
                  {txt => <option key={k.toString()} value={k}>{txt}</option>}
                </FormattedMessage>
              );
            })}
          </select>
        )
      },
      {
        Header: (size > 800) ? <FormattedMessage id="FAVORITE" /> : "FAV",
        filterable: false,
        sortable: false,
        width: (size > 800) ? 100 : 50,
        style: {
          textAlign: "Center"
        },
        Cell: row => (


          <Favorites ListaFavoritos={row.original.athlete.athlete_id} />

        )
      }
    ];
    return (
      <ReactTable
        defaultSorted={[
          {
            id: "dorsal_number",
            desc: false
          }
        ]}
        previousText={<FormattedMessage id="PREVIOUS" />}
        nextText={<FormattedMessage id="NEXT" />}
        columns={columns}
        data={this.props.athleteList}
        filterable
        noDataText={<FormattedMessage id="NODATA" />}
        defaultPageSize={minimalSize(this.props.athleteList.length,20)}
        pageSizeOptions={calculatePagination(this.props.athleteList.length, 20)}
        showPagination={true}
        showPaginationTop={false}
        showPaginationBottom={true}
        showPageSizeOptions={true}
        showPageJump={true}
        collapseOnSortingChange={true}
        collapseOnPageChange={true}
        collapseOnDataChange={true}
        freezeWhenExpanded={false}
        sortable={true}
        multiSort={true}
        resizable={true}
        pageText={<FormattedMessage id="PAGE" />}
        defaultSortDesc={false}
        rowsText={""}
        className="-striped -highlight"

      />
    );
  }
}
