import React, { Component } from 'react'

export default class FasterMale extends Component {
    render() {

        return (

            <div>
                {/* Info boxes */}
                <div className="col-md-3 col-sm-6 col-xs-12">
                    <div className="info-box">
                        <span className={`info-box-icon bg-${this.props.color}`}><i className="fa fa-refresh fa-spin" /></span>
                        <div className="info-box-content">
                            <span className="info-box-text">{this.props.text}</span>
                        </div>
                        {/* /.info-box-content */}
                    </div>
                    {/* /.info-box */}
                </div>

            </div>
        )
    }
}
