export function calculatePagination(size, minimalSize) {
    var sizeArray = []
    if(minimalSize<size){
        sizeArray.push(minimalSize)
    }
    if (size > 50) {
        sizeArray.push(50)
    }
    if (size > 100) {
        sizeArray.push(100)
    }
    if (size > 200) {
        sizeArray.push(200)
    }
    if (size > 500) {
        sizeArray.push(500)
    }
    if (minimalSize != size) {
        sizeArray.push(size)
    }
    return (sizeArray)
}

export function minimalSize(size, minimalSize) {
    var sizeArray
    if(size<minimalSize){
        sizeArray=size
    }
    else{
        sizeArray=minimalSize
    }
    

    return (sizeArray)
}

