import React, { Component } from 'react'
import { Line } from 'react-chartjs-2';
import { FormattedMessage} from 'react-intl';

var data;

export default class Position extends Component {

    chartDataVerification() {

        if (!!(this.props.Global) && !!(this.props.Tier) && !!(this.props.Gender)) {
            return (<Line
                data={data}

                options={{
                    legend: {
                        display: true
                    },
                    scales: {
                        yAxes: [{
                            ticks: {
                                max: this.props.maxY,
                                min: 0,
                                stepSize: 1
                            }
                        }]
                    },
                }} />)

        }
        else {
            return (<p><FormattedMessage id="NODATA" /></p>)


        }
    }

    render() {
        var position = []
        this.props.checkpoint.map(data => { position.push(data.local) })
        if (!!(this.props.Global) && !!(this.props.Tier) && !!(this.props.Gender)) {
            data = {
                labels: (position.length > 2) ? position.slice(1) : position,
                datasets: [
                    {
                        label: 'Global',
                        fill: false,
                        lineTension: 0.1,
                        backgroundColor: '#455A64',
                        borderColor: '#455A64',
                        borderCapStyle: 'butt',
                        borderDash: [],
                        borderDashOffset: 0.0,
                        borderJoinStyle: 'miter',
                        pointBorderColor: '#455A64',
                        pointBackgroundColor: '#455A64',
                        pointBorderWidth: 1,
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: '#455A64',
                        pointHoverBorderColor: '#455A64',
                        pointHoverBorderWidth: 2,
                        pointRadius: 1,
                        pointHitRadius: 10,
                        data: (this.props.Global.length > 2) ? this.props.Global.slice(1) : this.props.Global
                    },
                    {
                        label: 'Genero',
                        fill: false,
                        lineTension: 0.1,
                        backgroundColor: 'rgba(75,192,192,0.4)',
                        borderColor: 'rgba(75,192,192,1)',
                        borderCapStyle: 'butt',
                        borderDash: [],
                        borderDashOffset: 0.0,
                        borderJoinStyle: 'miter',
                        pointBorderColor: 'rgba(75,192,192,1)',
                        pointBackgroundColor: 'rgba(75,192,192,1)',
                        pointBorderWidth: 1,
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                        pointHoverBorderColor: 'rgba(220,220,220,1)',
                        pointHoverBorderWidth: 2,
                        pointRadius: 1,
                        pointHitRadius: 10,
                        data: (this.props.Gender.length > 2) ? this.props.Gender.slice(1) : this.props.Gender
                    },
                    {
                        label: 'Escalo',
                        fill: false,
                        lineTension: 0.1,
                        backgroundColor: '#9CCC65',
                        borderColor: '#9CCC65',
                        borderCapStyle: 'butt',
                        borderDash: [],
                        borderDashOffset: 0.0,
                        borderJoinStyle: 'miter',
                        pointBorderColor: '#9CCC65',
                        pointBackgroundColor: '#9CCC65',
                        pointBorderWidth: 1,
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: '#9CCC65',
                        pointHoverBorderColor: '#9CCC65',
                        pointHoverBorderWidth: 2,
                        pointRadius: 1,
                        pointHitRadius: 10,
                        data: (this.props.Tier.length > 2) ? this.props.Tier.slice(1) : this.props.Tier
                    }

                ]

            };


        }



        return (
            this.chartDataVerification()

        )
    }
}
