import React, { Component } from "react";
import ReactTable from "react-table";
import "react-table/react-table.css";
import { Link } from "react-router-dom";
import Favorites from "../favorites/favoritesChecker"
import { FormattedMessage } from 'react-intl';
import { calculatePagination } from "../Scripts/PaginationHelper"
import { minimalSize } from "../Scripts/PaginationHelper"

var size = window.innerWidth;


export default class TableGiveUP extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: this.props.loading
    };
  }
  CheckSize() {
    if (size > 800) {
      return true;
    }
    else {
      return false;
    }
  }
  render() {
    //Info for the filters
    //get the category
    let category = this.props.athleteList.map(athelete => {
      return athelete.escalao;
    });
    //remove duplicates
    const Tableuniquecategory = [...new Set(category.sort())];
    //get the nacionality
    let nacionality = this.props.athleteList.map(athelete => {
      return athelete.country;
    });
    //remove duplicates
    const Tableuniquenacionality = [...new Set(nacionality.sort())];
    //get the Local
    let local = this.props.athleteList.map(athelete => {
      return athelete.local;
    });
    //remove duplicates
    const Tableuniquelocal = [...new Set(local.sort())];
    //get the Club
    let club = this.props.athleteList.map(athelete => {
      return athelete.club;
    });
    //remove duplicates
    const Tableuniqueclub = [...new Set(club.sort())];
    //Table
    const columns = [
      {
        Header: "Id",
        accessor: "athlete_id",
        sortable: false,
        style: {
          textAlign: "Center"
        },
        width: 100,
        maxWidth: 100,
        mimWidth: 100,
        show: false
      },
      {
        Header: (size>800)? <FormattedMessage id="DORSAL" />:"PTL",
        id: "peitoral",
        accessor: d => Number(d.peitoral),
        style: {
          textAlign: "Center"
        },
        width:(size>800)?100:90,
        Filter: ({ filter, onChange }) => (
          <FormattedMessage id="SEARCH" >
          {txt => <input
             onChange={event => onChange(event.target.value)}
             value={filter ? filter.value : ""}
             placeholder={txt}
             style={{
               width: "100%",
               textAlign: "Center"
             }}
           />}
           </FormattedMessage>
        ),
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_id}` }}
          >
            <span style={{ color: "#3c8dbc" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: <FormattedMessage id="NAME" />,
        accessor: "name",
        width:(size>800)?300:175,
        style: {
          textAlign: "Center"
        },
        Filter: ({ filter, onChange }) => (
          <FormattedMessage id="SEARCH" >
          {txt => <input
             onChange={event => onChange(event.target.value)}
             value={filter ? filter.value : ""}
             placeholder={txt}
             style={{
               width: "100%",
               textAlign: "Center"
             }}
           />}
           </FormattedMessage>
        ),
        Cell: row => (

          <Link
            to={{ pathname: `/AthleteDetail/${this.props.eventID}/${this.props.competitionID}/${row.original.athlete_id}` }}
          >
            <span style={{ color: "#3c8dbc" }}>{row.value} </span>
          </Link>
        )
      },
      {
        Header: <FormattedMessage id="TEAM" />,
        accessor: "club",
        show : this.CheckSize(),
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniqueclub.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%",textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
             <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniqueclub.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: <FormattedMessage id="CATEGORY" />,
        accessor: "escalao",
        width:(size>800)?150:100,
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquecategory.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%",textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniquecategory.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },

      {
        Header: <FormattedMessage id="COUNTRY" />,
        accessor: "country",
        width:150,
        show : this.CheckSize(),
        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquenacionality.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%",textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
           <FormattedMessage id="ALL">
              {txt => <option value="all">{txt}</option>}
            </FormattedMessage>
            {Tableuniquenacionality.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },
      {
        Header: <FormattedMessage id="PLACE" />,
        accessor: "local",
        width:(size>800)?150:100,

        style: {
          textAlign: "Center"
        },
        Cell: row => row.value,
        filterMethod: (filter, row) => {
          if (filter.value === "") {
            return true;
          }

          if (Tableuniquelocal.includes(filter.value)) {
            return row[filter.id] === filter.value;
          } else {
            return true;
          }
        },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%",textAlignLast:"center" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all">Todos</option>
            {Tableuniquelocal.map(k => {
              return (
                <option key={k.toString()} value={k}>
                  {k}
                </option>
              );
            })}
          </select>
        )
      },

      {
        Header: (size>800)? <FormattedMessage id="FAVORITE" />:"FAV",
        width:(size>800)?100:50,

        filterable: false,
        sortable: false,

        style: {
          textAlign: "Center"
        },
        Cell: row => (
          <Favorites ListaFavoritos={row.original.athlete_id} />

        )
      }
    ];

    return (
      <ReactTable
        defaultSorted={[
          {
            id: "peitoral",
            desc: false
          }
        ]}
        previousText={<FormattedMessage id="PREVIOUS" />}
        nextText={<FormattedMessage id="NEXT" />}
        columns={columns}
        data={this.props.athleteList}
        filterable
        noDataText={<FormattedMessage id="NODATA" />}
        defaultPageSize={minimalSize(this.props.athleteList.length,4)}
        pageSizeOptions={calculatePagination(this.props.athleteList.length, 4)}
        showPaginationTop={false}
        showPaginationBottom={true}
        showPageSizeOptions={true}
        showPageJump={true}
        collapseOnSortingChange={true}
        collapseOnPageChange={true}
        collapseOnDataChange={true}
        freezeWhenExpanded={false}
        sortable={true}
        multiSort={true}
        resizable={true}
        defaultSortDesc={false}
        pageText={<FormattedMessage id="PAGE" />}
        rowsText={""}
        className="-striped -highlight"

      />
    );
  }
}
