import React, { Component } from "react";
import LanguagueSelector from "./LanguageSelector"

const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;
var size = window.innerWidth;

export default class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      EventInfo: [],
      loading: false,
    };
    this.getEventInfo();
  }
  CheckSize() {
    if (size < 800) {
      return (
        <a style={{fontSize:"22px"}}
          href="##"
          className="sidebar-toggle"
          data-toggle="push-menu"
          role="button"
        >
          <span  className="sr-only">Toggle navigation</span>
        </a>

      )
    }
  }
  getEventInfo = async e => {

    const API_CALL = await fetch(
      `${API_URL}/trail/Event/${this.props.eventID}/`,
      {
        headers: new Headers({
          Authorization: TOKEN
        })
      }
    );
    const data = await API_CALL.json();
    this.setState({ EventInfo: data, loading: true });

  };

  componentWillMount() {
    if(localStorage.getItem('State')){
      if(JSON.parse(localStorage.getItem('EventID'))===this.props.eventID){
        const rehydrate = JSON.parse(localStorage.getItem('State'))
        this.setState(rehydrate)
      }
      else{
        this.getEventInfo();
        this.CheckSize();

      }
    }
    else{
      this.getEventInfo();
      this.CheckSize();
    }

  }
  componentWillUnmount() {
    localStorage.setItem('State', JSON.stringify(this.state))
    localStorage.setItem('EventID',JSON.stringify(this.props.eventID))
  }

  GoToHomePage() {
    window.location = '/';
  }
  render() {
    return (
      <div>
        <header className="main-header">
          {/* Logo */}
          <div className="logo">

            {/* mini logo for sidebar mini 50x50 pixels */}
            <span className="logo-mini">
              <a href="#" onClick={() => this.GoToHomePage()}><img src="/logo_ts_w_32.png"/></a>
            </span>
            {/* logo for regular state and mobile devices */}
            <span style={{fontSize:"22px"}} className="logo-lg">
              <a href="#" onClick={() => this.GoToHomePage()}> <img src="/logo_ts_w_32.png"></img> <strong>Timing</strong> System</a>
            </span>
          </div>
          {/* Header Navbar: style can be found in header.less */}
          <nav className="navbar navbar-static-top">
            {/* Sidebar toggle button*/}
            {this.CheckSize()}
            {/* Navbar Right Menu */}
            <div className="navbar-custom-menu">
              <ul className="nav navbar-nav">

              {/* imagem aqui maximo 100 width */}
                <li className="dropdown user user-menu">
                

                  <div style={{ float: "left" }} className="logoText" >
                    
                  <img src={this.state.EventInfo.eventLogo_url} class="margin" style={{height: size >800 ? "60px" : "40px", width: "auto" }}></img> 

                    <strong>{this.state.EventInfo.name}</strong> 
                  </div>
                  
                </li>
              </ul>
            </div>
          </nav>
        </header>
      </div >
    );
  }
}
