import React from "react";
import AliceCarousel from "react-alice-carousel";
import "react-alice-carousel/lib/alice-carousel.css";
var size = window.innerWidth;
class Carousel extends React.Component {
 
  getSize(){
    var responsive={};

    if(size >1000){
      return(responsive = {
        0: { items: 1 },
        1: { items: 2 }
      })
    }
    else{
     return( responsive = {
        0: { items: 1 }
      })

    }
  }

  render() {

    
    const handleOnDragStart = e => e.preventDefault();
    return (
      
      <AliceCarousel
        autoPlay={true}
        responsive={this.getSize()}
        fadeOutAnimation={true}
        mouseDragEnabled={true}
        buttonsDisabled={true}
        dotsDisabled={true}
        autoPlayInterval={2000}
      >
        <img
          alt=""
          className="Carousel"
          src="https://i.imgur.com/sBM8dyK.jpg"
          onDragStart={handleOnDragStart}
        />
        <img
          alt=""
          className="Carousel"
          src="https://i.imgur.com/9xdqNjl.jpg"
          onDragStart={handleOnDragStart}
        />
        <img
          alt=""
          className="Carousel"
          src="https://i.imgur.com/WbGrJzo.jpg"
          onDragStart={handleOnDragStart}
        />
                <img
          alt=""
          className="Carousel"
          src="https://i.imgur.com/sBM8dyK.jpg"
          onDragStart={handleOnDragStart}
        />
                <img
          alt=""
          className="Carousel"
          src="https://i.imgur.com/WbGrJzo.jpg"
          onDragStart={handleOnDragStart}
        />
      </AliceCarousel>
    );
  }
}
export default Carousel;
