import React, { Component } from "react";
import { FormattedMessage } from 'react-intl';
export default class NumberTeams extends Component {
    render() {
        //get the Club
        let club = this.props.athleteList.map(athelete => {
            return athelete.club;
        });
        //remove duplicates
        const Tableuniqueclub = [...new Set(club)];
        return (
            <div>
                {/* Info boxes */}
                <div className="col-md-3 col-sm-6 col-xs-12">
                    <div className="info-box">
                        <span className="info-box-icon bg-yellow"><i className="ion ion-ios-people-outline" /></span>
                        <div className="info-box-content">
                            <span className="info-box-text"><FormattedMessage id="TEAMS" /></span>
                            <span className="info-box-number">{Tableuniqueclub.length}</span>
                        </div>
                        {/* /.info-box-content */}
                    </div>
                    {/* /.info-box */}
                </div>

            </div>
        )
    }
}
