import React, { Component } from 'react'
import { Bar } from 'react-chartjs-2';

var options;
var data;

export default class GenderComp extends Component {
    render() {
             data = {
                labels: this.props.data.map(items => { return (items.Label) }),
                datasets: [
                    {
                        label: this.props.Inscription,
                        fill: false,
                        lineTension: 0.1,
                        backgroundColor: [
                            "#64B5F6", "#9575CD",
                        ],
                        pointBorderColor: '#455A64',
                        pointBackgroundColor: '#455A64',
                        pointBorderWidth: 1,
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: '#455A64',
                        pointHoverBorderColor: '#455A64',
                        pointHoverBorderWidth: 2,
                        pointRadius: 1,
                        pointHitRadius: 10,
                        data: this.props.data.map(items => { return (items.Value) })
                    },
                    {
                        label: this.props.Start,
                        fill: false,
                        lineTension: 0.1,
                        backgroundColor: [
                            "#64B5F6", "#9575CD",
                        ],
                        pointBorderColor: '#455A64',
                        pointBackgroundColor: '#455A64',
                        pointBorderWidth: 1,
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: '#455A64',
                        pointHoverBorderColor: '#455A64',
                        pointHoverBorderWidth: 2,
                        pointRadius: 1,
                        pointHitRadius: 10,
                        data: this.props.dataStart.map(items => { return (items.Value) })
                    },
                    {
                        label: this.props.Arrivals,
                        fill: false,
                        lineTension: 0.1,
                        backgroundColor: [
                            "#64B5F6", "#9575CD",
                        ],
                        pointBorderColor: '#455A64',
                        pointBackgroundColor: '#455A64',
                        pointBorderWidth: 1,
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: '#455A64',
                        pointHoverBorderColor: '#455A64',
                        pointHoverBorderWidth: 2,
                        pointRadius: 1,
                        pointHitRadius: 10,
                        data: this.props.dataLast.map(items => { return (items.Value) })
                    }
                ]

            }
        


        options = {
            legend: {
                display: false,
            },
            scales: {
                yAxes: [{
                  ticks: {
                    beginAtZero: true,
                    min: 0
                  }    
                }]
              }   
        };

        return (
            <div>
            <Bar data={data} options={options} />
        </div>
        )
    }
}
