import React, { useState } from 'react'
import {IntlProvider} from "react-intl"
import Portuguese from "./../languages/pt.json"
import Spanish from "./../languages/es.json"
import French from "./../languages/fr.json"
import English from "./../languages/en.json"


export const Context = React.createContext();
var local
let lang;
//check if is save any languague
if(!!localStorage.getItem("Languague")){
  local=localStorage.getItem("Languague");
}
else{
  local = navigator.language.substring(3,5).toUpperCase()
}
if (local === "PT") {
  lang = Portuguese;
}
else if (local === "ES") {
  lang = Spanish;
}
else if (local === "FR") {
  lang = French;
}
else if (local === "GB") {
    lang = English;
  }
else {
  local = "GB"
  lang = English;
}

 const Wrapper=(props) => {

  const[locale,setLocale]= useState(local);
  const [messages,setMessages]= useState(lang);

  function selectLang(language){
    const newLocale =language ;
    //remote from local storage the old one
    localStorage.removeItem("Languague")
    //add new languague to storage
    localStorage.setItem("Languague", language)
    setLocale(newLocale)
    if (newLocale === "PT") {
      setMessages(Portuguese)
    }
    else if (newLocale === "ES") {
      setMessages(Spanish)
    }
    else if (newLocale === "FR") {
      setMessages(French)
    }
    else if (newLocale === "GB") {
      setMessages(English)
      }
    else {
      setMessages(English)
    }
  }

    return(
      <Context.Provider value={{locale, selectLang}}>
        <IntlProvider messages={messages} locale={locale}>
            {props.children}
        </IntlProvider>
        </Context.Provider>
    )

}

export default Wrapper