
import React, { Component, useContext } from "react";
import { Context } from "../../languages/Wrapper"
import ReactFlagsSelect from 'react-flags-select';
import "../../languages/flags/react-flags-select.css";


const LanguageSelector = (props) => {


  const context = useContext(Context)
  return (
    <div>
      <ReactFlagsSelect  defaultCountry={context.locale}  onSelect={(countryCode) => context.selectLang(countryCode)}
        countries={["PT", "GB", "FR", "ES"]}
        showSelectedLabel={false}
        showOptionLabel={false}
      />

    </div>


  );
}
export default LanguageSelector
