import React, { Component } from 'react'
import { Line } from 'react-chartjs-2';
import { FormattedMessage} from 'react-intl';

var data;

export default class Velocity extends Component {

  chartDataVerification() {
    
    if (!!(this.props.Faster) && !!(this.props.Normal)) {
      return (<Line data={data} />)

    }
    else {
      return (<p><FormattedMessage id="NODATA" /></p>)


    }
  }


  render() {
    if (!!(this.props.Normal) && !!(this.props.Faster)) {
      var position = []
      this.props.checkpoint.map(data => { position.push(data.local) })
      data = {
        labels: (position.length > 2) ? position.slice(1) : position.Normal,
        datasets: [
          {
            label: this.props.Name,
            fill: false,
            lineTension: 0.1,
            backgroundColor: '#455A64',
            borderColor: '#455A64',
            borderCapStyle: 'butt',
            borderDash: [],
            borderDashOffset: 0.0,
            borderJoinStyle: 'miter',
            pointBorderColor: '#455A64',
            pointBackgroundColor: '#455A64',
            pointBorderWidth: 1,
            pointHoverRadius: 5,
            pointHoverBackgroundColor: '#455A64',
            pointHoverBorderColor: '#455A64',
            pointHoverBorderWidth: 2,
            pointRadius: 1,
            pointHitRadius: 10,
            data: (this.props.Normal.length > 2) ? this.props.Normal.slice(1) : this.props.Normal
          },
          {
            label: 'Mais Rpido',
            fill: false,
            lineTension: 0.1,
            backgroundColor: 'rgba(75,192,192,0.4)',
            borderColor: 'rgba(75,192,192,1)',
            borderCapStyle: 'butt',
            borderDash: [],
            borderDashOffset: 0.0,
            borderJoinStyle: 'miter',
            pointBorderColor: 'rgba(75,192,192,1)',
            pointBackgroundColor: 'rgba(75,192,192,1)',
            pointBorderWidth: 1,
            pointHoverRadius: 5,
            pointHoverBackgroundColor: 'rgba(75,192,192,1)',
            pointHoverBorderColor: 'rgba(220,220,220,1)',
            pointHoverBorderWidth: 2,
            pointRadius: 1,
            pointHitRadius: 10,
            data: (this.props.Faster.length > 2) ? this.props.Faster.slice(1) : this.props.Faster
          },
          
        ]

      };
    }



    return (
      this.chartDataVerification()

    )
  }
}
