import React, { Component } from 'react'
import { Line } from 'react-chartjs-2';

var data;
var options;
export default class Ground extends Component {
    render() {
            data = {
            labels:this.props.data.map(items => { return (items.checkpoint.local) }),
            datasets: [
              {
                label: this.props.Name,
                fill: true,
                lineTension: 0.1,
                backgroundColor: '#BCAAA4',
                borderColor: '#66BB6A',
                borderDash: [],
                borderDashOffset: 0.0,
                borderJoinStyle: 'miter',
                pointBorderColor: '#212121',
                pointBackgroundColor: '#212121',
                pointBorderWidth: 1,
                pointHoverRadius: 5,
                pointHoverBackgroundColor: '#212121',
                pointHoverBorderColor: '#212121',
                pointHoverBorderWidth: 2,
                pointRadius: 1,
                pointHitRadius: 10,
                data: this.props.data.map(items => { return (items.checkpoint.altitude) })
              }              
            ]
          };
          
        options = {
            legend: {
                display: false,
            },
            scales: {
                yAxes: [{
                  ticks: {
                    beginAtZero: true,
                    min: 0,
                  },   
                  scaleLabel: {
                    display: true,
                    labelString: 'm'
                  } 
                }]
              }   
        };
        
    
    
    
        return (
            <Line data={data} options={options} />
    
        )
      }
}
